<template>
  <div class="contentHtml">
  	  <van-cell-group>
  	  	<van-cell title="需求类型" is-link v-on:click="popShow('wangdian')" :value="wangdian.val"/>
  	  	<van-field  label="标题" v-model="title" placeholder="请输入" required/>
  	  	<van-cell-group>
		  <van-switch-cell v-model="checked" title="资金托管" />
		</van-cell-group>
	  	<van-field type="number"  v-model="money" label="我的预算" placeholder="请输入" required/>
	    <van-cell title="截止时间" is-link v-on:click="popShow('currentDate')" :value="currentDate"/>
	    <van-field  label="附件上传" placeholder="请输入" required/>
	    <div class="addList" style="padding-bottom:80px;">
			<h3 class="imgH3" data-id0="" data-id1="" data-id2="">
	  			<span style="color:red;">*</span>
	  			<span class="inputLeft">需求描述:</span>
	  		</h3>
	  		<textarea v-model="excerpt" id="ksjkds" type="" placeholder="请输入"></textarea>
	   </div>
	  </van-cell-group>
	  
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="commintBut">提交审核</div>
	 </div>
	 <!--网店类型-->
	 <van-popup v-model="wangdian.type" position="bottom" :overlay="true">
		<van-picker :columns="wangdian.list" show-toolbar :title="wangdian.title" @cancel="onCancel()" @confirm="wangdianOnConfirm" /> 
	</van-popup>
	<!--时间-->
	<van-popup v-model="timeBox.type" position="bottom" :overlay="true">
		<van-datetime-picker type="datetime" :min-hour="timeBox.minHour"  :max-hour="timeBox.maxHour"  :min-date="timeBox.minDate" @cancel="onCancel()" @confirm="timeOnConfirm"/>
  	</van-popup>
  </div>
</template>

<script>
$("title").text("出售网店");
export default {
  name: 'contentHtml',
  data () {
    return {
    		checked: false,
    		excerpt:'',
    		money:"",
    		currentDate:"",
    		title:"",
    		wangdian: {
    			"list":['违规扣分', '淘宝客', '客服外包', '买家秀', '打造爆款', '模特', '代运营', '摄影', '续签', '网店装修'],
    			"title":"选择网店类型",
    			"val":"违规扣分",
    			"index":1,
    			"type":false
    		},
    		timeBox:{
    		  "type":false,
	      "minHour": 1,
	      "maxHour": 23,
	      "minDate": new Date(),
	      "maxDate": new Date(2019, 10, 1),
	      "currentDate": new Date(2018, 0, 1)
	    },
    }
  },
  created:function(){
  	var _this = this;
  },
  methods: {
  	commintBut(){
  		var _this = this;
		if(!_this.title){
			_this.utilMsg("请填写标题")
			return false;
		}else if(!_this.excerpt){
			_this.utilMsg("请填写描述")
			return false;
		}else if(!_this.money){
			_this.utilMsg("请填写预算")
			return false;
		}else if(!_this.currentDate){
			_this.utilMsg("请填写过期时间")
			return false;
		}
		var getUser = localStorage.user;
		if(getUser){
  	 		var user_id =  JSON.parse(getUser).userid;
  	 	}else{
  	 		_this.utilMsg("请先登录");
  	 		return false;
  	 	}
  		$("#msgLoding").show();
    		var datas = {
	        "classid":59,
	        "userid":user_id,
	        "type":_this.wangdian.index,      //需求类型1违规扣分2淘宝客3客服外包4买家秀5打造爆款6模特7代运营8摄影9续签10网店装修
	        "title":_this.title,    //求购标题
	        "content":_this.excerpt,   //求购内容
	        "budget_start":_this.money,		//预算开始
	        "budget_end":_this.money,			//预算结束
	        "buynum":1,					//购买数量
	        "stop_time":_this.currentDate,//截止日期
	        "price":_this.money
	    };
	    $.post(_this.apiUrl+'/e/wxapi/public_demand.php',datas,function(res){
	  		console.log(res)
	  		$("#msgLoding").hide();
    			_this.utilMsg("发布成功")
	  		//ios跳转页面
//			if(_this.utilIsLocalhost()==false){
//				var content = {"type":"hrefTo","link":'val',"title":'title',}
//			    	var data = JSON.stringify(content);
//			    ttf.nslog(data)
//		  	}else{
//		  		_this.$router.push(val)
//	  		}
		},"json")
  		
  	},
    popShow: function (type) {
    		if(type == "wangdian"){
    			this.wangdian.type = true
    		}else if(type == "currentDate"){
    			this.timeBox.type = true
    		}
  		this.bankCard = false;
  	},
  	wangdianOnConfirm(value, index) {
      this.wangdian.val = value;
      this.wangdian.index = index+1;
      this.wangdian.type = false;
    },
    onCancel(type) {
    		this.wangdian.type = false;
    		this.timeBox.type = false;
    },
    timeOnConfirm(value, index) {
      var myDate = value;
    	  var Y = myDate.getFullYear()//获取完整的年份(4位,1970-????)
    	  var M =  myDate.getMonth();//获取当前月份(0-11,0代表1月)
    	  var D = myDate.getDate()//获取当前日(1-31)
    	  var h = myDate.getHours(); //获取当前小时数(0-23)
    	  var m = myDate.getMinutes(); //获取当前分钟数(0-59)
    	  var s = myDate.getSeconds(); //获取当前秒数(0-59)
    	  var currentDate = Y+"-"+initTime(M)+"-"+initTime(D)+" "+initTime(h)+":"+initTime(m)+":"+initTime(s);
    	  function initTime(val){
    	  	var vals = val;
    	  	if(val < 10){
    	  		return "0"+vals;
    	  	}else{
    	  		return vals;
    	  	}
    	  }
    	  this.currentDate = currentDate;
      this.timeBox.type = false;
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>
	
</style>



