<template>
  <div class="contentHtml">
  	<div class="flexLeft topBox">
  		<div class="imgText">
  			<img v-bind:src="shopInfo.shop_img_url" />
  			<span class="text">运动/户外</span>
  		</div>
  		<div>
  			<div class="initTitle">{{shopInfo.title}}</div>
  			<p>出售价格：<span>¥{{shopInfo.price}}</span></p>
  			<p>消费者保证金：<span>¥{{shopInfo.bond2}}</span></p>
  			<p>技术年费：<span>¥{{shopInfo.technical_money_fee}}</span></p>
  			<p>发布时间：{{shopInfo.public_time}}</p>
  		</div>
  	</div>
  	<div style="height: 10px;"></div>
  	<van-cell-group>
	  <van-cell title="买家描述：" v-bind:label="shopInfo.content" />
	</van-cell-group>
  	<div style="height: 10px;"></div>
  	<div class="initBox">
  		<div class="rightLine">店铺信息一览表</div>
  		<div class="info25 color3"><span>{{shopInfo.bossname}}</span><span>{{shopInfo.bossname}}</span><span>{{shopInfo.cmp_addr}}</span><span>{{shopInfo.business}}</span></div>
  		<div class="info25 color9"><span>店铺信息</span><span>商城类型</span><span>所在地区</span><span>收藏人数</span></div>
  		<div class="info25 color3"><span>{{shopInfo.intime}}</span><span>R标</span><span>{{shopInfo.complete}}</span><span>是</span></div>
  		<div class="info25 color9"><span>入驻时间</span><span>商标类型</span><span>营业额完成率</span><span>是否带货</span></div>
  		<div class="info25 color3"><span>{{shopInfo.intime}}</span><span>R标</span><span>100%</span><span>是</span></div>
  		<div class="info25 color9"><span>入驻时间</span><span>商标类型</span><span>营业额完成率</span><span>是否带货</span></div>
  	</div>
  	<div style="height: 10px;"></div>
  	<div class="initBox">
  		<div class="rightLine">店评分</div>
  		<div style="background: #f7f7f7;color: #666;">
  			<div class="info25" style="background: #efefef;line-height:30px;"><span>天猫</span><span>天猫</span><span>天猫</span><span>344</span></div>
  			<div class="info30">宝贝与描述相符：<span class="but1">商城类型</span><span class="but3">所在地区</span></div>
	  		<div class="info30">卖家的服务态度：<span class="but1">R标</span><span class="but2">100%</span></div>
	  		<div class="info30">卖家发货的速度：<span class="but1">商标类型</span><span class="but2">营业额完成率</span></div>
	  		<br />
  		</div>
  	</div>
  	<div style="height: 10px;"></div>
  	<div class="initBox">
  		<div class="rightLine">店铺信息一览表</div>
  		<div class="info25 color3"><span>天猫</span><span>天猫</span><span>天猫</span><span>344</span></div>
  		<div class="info25 color9"><span>店铺信息</span><span>商城类型</span><span>所在地区</span><span>收藏人数</span></div>
  		<div class="info25 color3"><span>2017-07-23</span><span>R标</span><span>100%</span><span>是</span></div>
  		<div class="info25 color9"><span>入驻时间</span><span>商标类型</span><span>营业额完成率</span><span>是否带货</span></div>
  		<div class="info25 color3"><span>2017-07-23</span><span>R标</span><span>100%</span><span>是</span></div>
  		<div class="info25 color9"><span>入驻时间</span><span>商标类型</span><span>营业额完成率</span><span>是否带货</span></div>
  	</div>
  	  <div style="height: 10px;"></div>
	  <van-cell-group>
		  <van-cell title="服务流程：" label="司法考试返回拉萨和弗－>兰克萨哈夫了萨哈夫" />
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	   <van-submit-bar button-text="立即购买" @submit="onSubmit"/>
	 <!-- <van-submit-bar :price="3050" button-text="提交订单" @submit="onSubmit"/>-->
  </div>
</template>

<script>
setTimeout(function(){
	$("title").text("店铺详情");
},1)
export default {
  name: 'contentHtml',
  data () {
    return {
    		imageURL:'https://img.yzcdn.cn/2.jpg',
    		shopInfo:'',
    }
  },
  created:function(){
  	var _this = this;
  	var id = _this.$route.params.id;
  	$.get(_this.apiUrl+'/e/wxapi/get_shop_by_id.php',{"itemid":id},function(res){
	  		_this.shopInfo = res[0];
	  		console.log(_this.shopInfo)
		},'json')
  },
  methods: {
  	onSubmit:function(){
  		var _this = this;
  		if(localStorage.user){
    			var userid = JSON.parse(localStorage.user).userid;
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
    		_this.xiadang(userid);
  	}, 
	hrefTo(id){//ios跳转页面
		console.log(this.utilIsLocalhost())
		if(this.utilIsLocalhost()==false){
			var content = {"type":"href","id":id}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push('/information/'+id)
	  	}
	},
	//下单
	xiadang:function(userid){
		var _this = this;
  		var datas = {
	        "fk_user_id":userid,
	        "type":1,
	        "order_id":_this.$route.params.id
	    };
	    $("#msgLoding").show();
	    _this.utilAjax(_this.apiUrl+'/e/wxapi/xiadan.php',datas,function(res){
    			$("#msgLoding").hide();
    			if(_this.utilIsLocalhost()==false){
				var content = {"type":"hrefTo","link":"/user/order/orderDetail/"+res,"title":"订单详情"}
			    	var data = JSON.stringify(content);
			    ttf.nslog(data)
		  	}else{
		  		_this.$router.push('/user/order/orderDetail/'+res)
		  	}
		},"post","no");
	},
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.topBox{padding:15px;background: #fff;}
	.topBox p{color: #666;line-height: 22px;font-size: 12px;}
	.topBox span{color: #ff1818;}
	.imgText{position: relative;margin-right: 20px;}
	.imgText img{
		width:80px;height:80px;border-radius:50%;-webkit-border-radius: 50%;border: 2px solid #ccc;
		
	}
	.imgText .text{
		position: absolute;top:60px;left: 50%;width: 80px;line-height:24px;margin-left: -40px;
		text-align:center;background: #d51122;color: #fff;
	}
	.rightLine{border-left: 3px solid #D51122;font-size: 14px;padding-left: 10px;line-height: 14px;margin: 5px 0;}
	.info25 span{display:inline-block;width: 25%;text-align: center;font-size: 12px;padding:2px 0;}
	.info30{padding:2px 10px;line-height:20px ;margin-top: 10px;}
	.info30 .but1,.info30 .but2,.info30 .but3{display:inline-block;text-align: center;font-size: 12px;width: 30%;margin-left:5px;}
	.info30 .but1{border: 1px solid #eee;background: #fff;color: #D51122;}
	.info30 .but2{border: 1px solid #D51122;background: #D51122;color: #fff;}
	.info30 .but3{border: 1px solid #00a5e7;background: #00a5e7;color: #fff;}
</style>



