<template>
  <div class="contentHtml">
	<div class="initBox mb10">
		<div class="initTitle" style="padding: 10px;text-align: center;">———— 我的圈子 ————</div>
		<div class="flexLeft oh text-c">
			<template v-for="listData in myCircleS">
				<div class="fl width33" v-on:click="hrefTo('/circle/circleDetail/'+listData.id,'圈子详情')">
					<img v-bind:src="listData.head_img_url" width="112" height="85"/>
					<div class="myCircle">{{listData.cheng_yuan_count}}</div>
					<div class="text-oh1">{{listData.name}}</div>
				</div>
			</template>
		</div>
		<div class="colorA text-c font16 initBox" v-on:click="hrefTo('/circle/myCircle','我的圈子')">查看我的所有圈子</div>
	</div>
	<ul class="flex iconType">
		<li>
			<img v-on:click="hrefTo('/circle/circleAdd','创建圈子')" src="../../assets/chushouwangdian@3x.png"/>
			<p>创建圈子</p>
		</li>
		<li>
			<img v-on:click="hrefTo('/circle/circleFind','发现圈子')" src="../../assets/jingbiaohuida@3x.png"/>
			<p>发现圈子</p>
		</li>
		<li>
			<img v-on:click="hrefTo('/circle/circleNewsAdd/0','发表动态')" src="../../assets/wangdianpinggu@3x.png"/>
			<p>发表动态</p>
		</li>
		<li>
			<img v-on:click="hrefTo('/circle/circleNews','我的动态')" src="../../assets/jingbiaohuida@3x.png"/>
			<p>我的动态</p>
		</li>
	</ul>
  	<ul class="listBox">
  	  	<template v-for="(listData,key) in listDatas">
	  		<li class="mb10 initBox lineHeight line oh">
  				<div class="fl" style="width: 18%;">
	  				<img src="../../assets/chushouwangdian@2x.png" width="40" height="40"/>
	  			</div>
	  			<div class="color9 fl" style="width:82%;">
	  				<div><span>{{listData.nickname}}</span><span class="toFollow" v-bind:class="{ followOn: listData.is_guanzhu }" v-on:click="follow(listData.fk_user_id,key)">关注</span></div>
	  				<div class="color3">{{listData.text_content}}</div>
	  				<div>
	  					<template v-for="img in listData.img_content">
							<img  v-bind:src="img" height="85" width="85" style="margin-right:5px;"/>
						</template>
					</div>
	  				<div>
	  					<span>{{listData.time}}</span>
	  					<div class="operation">
	  						<img v-on:click="soperation(key)" src="../../assets/zhankai@2x2.png" width="33" height="22" />
	  						<div class="hideBox" v-bind:class="{hideSoperation : listData.hideSoperation}">
	  							<span v-on:click="fabulous(listData.id,key)" style="padding-right: 10px;"><img class="textImg" src="../../assets/dadianzan@2x.png" width="20" height="20"> 点赞</span>
	  							<span v-on:click="pinglun(listData.id,key)"><img class="textImg" src="../../assets/pinglun@2x.png" width="20" height="20"> 评论</span>
	  						</div>
	  					</div>
	  				</div>
	  				<div class="plAndDz">
	  					<template v-if="listData.dz[0]">
		  					<div style="padding: 5px;font-size: 14px;">
		  						<img class="textImg" src="../../assets/dadianzan@2x.png" width="15" height="15">
		  						<template v-for="dzUsername in listData.dz">
									{{dzUsername.username}}  
								</template>
		  					</div>
	  					</template>
	  					<template v-if="listData.pl[0]">
		  					<div style="padding: 5px;font-size: 14px;">
		  						<template v-for="plUsername in listData.pl">
									<div v-on:click="pingluns(plUsername.plz_id,plUsername.plid,key)">{{plUsername.username}} : <span class="color6">{{plUsername.pl_content}}</span></div>
									<template v-for="plUsernames in plUsername.hf">
										<div v-on:click="pingluns(plUsernames.hfz_id,plUsername.plid,key)">{{plUsernames.hfz}}<span class="color6">回复</span>{{plUsernames.bhfz}}:<span class="color6">{{plUsernames.hf_content}}</span></div>
									</template>
								</template>
		  					</div>
	  					</template>
	  					
	  				</div>
	  			</div>
	    		</li>
    		</template>
     </ul>
     <div class="pinglunDivBox">
     	<input class="pinglunDiv" type="text" placeholder="输入评论内容" autofocus="autofocus"/>
     	<span class="pinglunDivEnd">确认发送</span>
     </div>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("转让");
export default {
  name: 'contentHtml',
  data () {
    return {
	    myCircleS:[],
	    	listDatas:[],
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.myCircle();
	_this.getList();
  },
  methods: {
  	myCircle:function(){
  		var _this = this;
  		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.myCircleS.length;
    		var per_page = 3;
    		var data = {
	        "fufei_type":-1,    //圈子类型0免费，1付费 -1全部
	        "type":-1,       //圈子类型0初级小白1网赚项目2自媒体3项目投资4淘宝电商5微商6淘宝客7网红8广告策划9经营管理10流量玩家11站长12域名13网络技术14同城交流
	        "name":"",       //圈子名称查询字段
	        "username":"",    //创建者用户名搜索字段
	        "fk_user_id":user.userid,  //查询者id
	        'status':1,
	        "pageIndex":start,
	        "pageSize":per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/quanzi_list.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.myCircleS[start+i] = res[i];
		    }
			_this.$forceUpdate();
		},'json')
  	},
  	getList:function(){
  		var _this = this;
  		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 20;
    		var data = {
	        'userid':user.userid,
			'pageIndex':start,
			'pageSize':per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/new_dt.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		      _this.listDatas[start+i].img_content = JSON.parse(res[i].img_content);
		      _this.listDatas[start+i].hideSoperation = true;
		    }
			
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val);
  		}
	},
	soperation:function(key){
		if(this.listDatas[key].hideSoperation){
			this.listDatas[key].hideSoperation = false;
		}else{
			this.listDatas[key].hideSoperation = true;
		}
		this.$forceUpdate();
	},
	//评论
	pinglun:function(id,key){
		$(".pinglunDivBox").show();
		$(".pinglunDiv").click();
		var _this = this;
		_this.soperation(key);
		$(".pinglunDivEnd").click(function(e){
			var pl_content = $(".pinglunDiv").val();
			if(!pl_content){_this.utilMsg("请填写评论内容");return false;}
			if(localStorage.user){
	    			var user = JSON.parse(localStorage.user);
	    		}else{
	    			_this.utilMsg("请先登录");
	    			return false;
	    		}
	    		var data = {
		        'userid':user.userid,
				'dt_id':id,
				'pl_content':pl_content
		    };
		    $("#msgLoding").show();
		  	$.get(_this.apiUrl+'/e/wxapi/dt_pl.php',data,function(res){
		  		$("#msgLoding").hide();
		  		$(".pinglunDivBox").hide();
				_this.$forceUpdate();
			},'json')
		})
		
		
	},
	//评论
	pingluns:function(bhfz_id,plId,key){
		$(".pinglunDivBox").show();
		$(".pinglunDiv").click();
		var _this = this;
		$(".pinglunDivEnd").click(function(e){
			var pl_content = $(".pinglunDiv").val();
			if(!pl_content){_this.utilMsg("请填写评论内容");return false;}
			if(localStorage.user){
	    			var user = JSON.parse(localStorage.user);
	    		}else{
	    			_this.utilMsg("请先登录");
	    			return false;
	    		}
	    		var data = {
				'hfz_id':user.userid,
				'bhfz_id':bhfz_id,
				'pl_id':plId,
				'hf_content':pl_content
		    };
		    $("#msgLoding").show();
		  	$.get(_this.apiUrl+'/e/wxapi/dt_pl_hf.php',data,function(res){
		  		$("#msgLoding").hide();
		  		$(".pinglunDivBox").hide();
		  		
				_this.$forceUpdate();
			},'json')
		})
	},
	//点赞
	 fabulous:function(id,key){
	 	var _this = this;
	 	_this.soperation(key);
		var getUser = localStorage.user;
			if(getUser){
	  	 		var user_id =  JSON.parse(getUser).userid;
	  	 	}else{
	  	 		_this.utilMsg("请先登录");
	  	 		return false;
	  	 	}
	    		var datas = {
	    			'dt_id':id,
	    			'userid':user_id,
	    		}
	    		$("#msgLoding").show();
    			$.get(_this.apiUrl+'/e/wxapi/dt_d_zhan.php',datas,function(res){
    				$("#msgLoding").hide();
				if(_this.listDatas[key].is_zhan==true){
	    				_this.utilMsg(res.content);
	    				_this.listDatas[key].is_zhan = false;
	    			}else{
	    				_this.utilMsg(res.content);
	    				_this.listDatas[key].is_zhan = true;
	    			}
	    			_this.$forceUpdate();
			},'json')
    			
	},
  	//关注
    follow: function (id,key) {
    		var _this = this;
    		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg('请先登录');
  	 		return false;
  	 	}
  	 	if(user_id==id){
  	 		_this.utilMsg('不需要关注自己');
  	 		return false;
  	 	}
    		var bgz_user_id = id;
    		var data = {"fk_user_id":user_id,"bgz_user_id":bgz_user_id};
    		$("#msgLoding").show();
    		$.get(_this.apiUrl+'/e/wxapi/guanzhu.php',data,function(res){
			$("#msgLoding").hide();
			if(_this.listDatas[key].is_guanzhu){
    				$(".follow").text("关注");
    				_this.utilMsg('已取消关注');
    				_this.listDatas[key].is_guanzhu = false;
    			}else{
    				$(".follow").text("已关注");
    				_this.utilMsg('关注成功')
    				_this.listDatas[key].is_guanzhu = true;
    			}
		},'json')
    		
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.iconType{text-align: center;font-size: 1.3rem;background: #fff;margin-bottom:10px;}
	.iconType li{width:25%;padding:15px;}
	.iconType li img{width:3rem;height: 3rem;}
	.myCircle{margin-top: -30px;color: #fff;line-height: 30px;}
	
	
	.toFollow{float: right; color: #fff;padding:3px 6px;background: #D51122;border-radius:3px;-webkit-border-radius:3px;}
	.isFollow{background: #ccc;}
	.operation{float: right;position: relative;}
	.operation .hideBox{
		position: absolute;top: -5px;right:60px;background: rgba(0,0,0,0.8);width: 120px;color: #fff;font-size:14px;
		padding:10px;border-radius:5px;-webkit-border-radius:5px;
	}
	.hideSoperation{display: none;}
	.ishide{display: none;}
	
	.pinglunDivBox {position: fixed;bottom: 0;left: 0;right: 0;display: none;padding:10px;background: #ff1919;}
	.pinglunDiv{width: 70%;border-radius: 3px;-webkit-border-radius: 3px;padding:6px;}
	.pinglunDivEnd{width:20%;text-align: center;display: inline-block;color: #fff;font-size:16px;}
	.plAndDz{background: #ecffff;margin-top: 10px;color: #99aacc;}
	
	
</style>



