<template>
  <div class="contentHtml" style="margin-bottom:40px;">
  	<img v-bind:src="classroomDetail.lesson_head_img"/>
  	<div class="initBox mb10">
  		<div>价格：免费</div>
  		<div>时间：<span class="colorA">{{classroomDetail.start_time}}</span><span class="fr color9">人数:{{classroomDetail.lesson_count}}</span></div>
  	</div>
  	<div class="detailBox">
	  	<div class="idInfo" >
	  		<img class="fl imgBr" src="../../../assets/img.png" width="40" height="40"/>
	  		<span class="fl" style="margin-left: 10px;">
	  			<span>{{classroomDetail.nickname}}</span><br/>
	  			<span class="color9" style="line-height: 2.5rem;">两小时前</span>
	  		</span>
	  		<span class="follow" v-bind:class="{ followOn: isFollow }" v-on:click="follow">已关注</span>
	  	</div>
	  	<div class="initTitle line" style="padding-bottom: 10px;">课堂简介：</div>
	  	<div class="color9 font14 initBox line">
		  	<div >课程大纲：</div>
		  	<div class="color9 font14" style="line-height: 40px;margin-left: 10px;">
		  		{{classroomDetail.smalltext}}
		  	</div>
	  	</div>
	  	<div class="initBox oh text-c font18">
	  		<div class="fl width50">
	  			<span>{{classroomDetail.lesson_count}}</span><br /><span class="color9">课程数</span>
	  		</div>
	  		<div class="fl width50">
	  			<span>{{classroomDetail.guanzhu_count}}</span><br /><span class="color9">关注人数</span>
	  		</div>
	  	</div>
	</div>
  	<div class="bottomNav">进入课堂</div>
  </div>
</template>
<script type="text/javascript">
 $("title").text("资讯详情");
export default {
  name: 'contentHtml',
  data () {
    return {
    		classroomDetail:'',
    		isFollow: true,
    }
  },
  created:function(){
  	var _this=this;
  	var user_id = '';
  	if(localStorage.user){
 		user_id =  JSON.parse(localStorage.user).userid;
 	}
  	//获取课堂详情
  	$.get(_this.apiUrl+'/e/wxapi/get_ketang_by_id.php',{
  		"itemid":_this.$route.params.id,
  		"fk_user_id":user_id,
  	},function(res){
  		console.log(res)
  		_this.classroomDetail = res[0];
	},"json")
  	
  },
  methods: {
  	//关注
    follow: function (event) {
    		var _this = this;
    		$("#msgLoding").show();
    		var user_id = this.datas.id;
    		if(_this.isFollow){
    			var status = '0';//0取消关注 1关注
    		}else{
    			var status = '1';//0取消关注 1关注
    		}
    		var data = {"user_id":user_id,"status":status};
    		this.utilAjax('http://121.14.27.27:88/wp-json/attention/v1/user',data,function(res){
    			$("#msgLoding").hide();
    			if(_this.isFollow){
    				$(".follow").text("关注");
    				_this.utilMsg('已取消关注');
    				_this.isFollow = false;
    			}else{
    				$(".follow").text("已关注");
    				_this.utilMsg('关注成功')
    				_this.isFollow = true;
    			}
			console.log(res)
		},"post");
    },
	hrefTo:function(id){//ios跳转页面
	  	
	}
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.subTite{line-height:50px;border-bottom: 1px solid #f7f7f7;text-align: center;}
	.detailBox{background: #fff;margin-bottom: 10px;padding: 10px;}
	.detailBox .title{font-size: 1.5rem;color: #333;}
	.detailBox .idInfo{overflow: hidden;margin: 30px 0;}
	.detailBox .idInfo .follow{float: right;padding: 6px 10px;color: #ff1919;border: 1px solid #ff1919;border-radius: 5px;-webkit-border-radius: 5px;}
	.detailBox .idInfo .followOn{color: #ccc;border: 1px solid #ccc;}
	
	.detailBox .content{font-size: 1.2rem;color: #666;}
	.shareBox .title{line-height:50px;border-bottom: 1px solid #f7f7f7;}
	.shareBox .iconBox{display:flex;display:-webkit-flex;justify-content:space-around;padding: 10px 0;}
	
	.commintBox li{padding: 10px 0;border-bottom: 1px solid #f7f7f7;}
	.commintBox li .authorInfo{color: #999;overflow: hidden;margin-bottom: 10px;}
	.commintBox li .text{margin-left:40px ;font-size: 1.5rem;}
	.bottomNav{
		line-height:50px;position: fixed;
		bottom: 0;left:0;width: 100%;
		text-align: center;
		background: #ff1919;color: #fff;
		font-size: 16px;
	}
</style>



