<template>
  <div class="contentHtml">
  	<ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li class="flexLeft" v-on:click="hrefTo('hrefTo','/index/transfer/shopInfo/'+listData.id,'网店详情')">
	  			<div class="rightBox"><img v-bind:src="listData.head_img_url"></div>
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.name}}</div>
	    				<div class="color9 text-oh2" style="height:32px;margin-top: 5px;">{{listData.content}}</div>
	    				<div class="listInfo">
	    					<span>ID：{{listData.id}}&emsp;&emsp;成员数：{{listData.cheng_yuan_count}}</span>
	    				</div>
	    			</div>
	    			<div><span class="addcircle">加入</span></div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("转让");
export default {
  name: 'contentHtml',
  data () {
    return {
	    	listDatas:[],
	    	shop_type:-1,
	    	message:'',
	    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 20;
    		var data = {
	        "fufei_type":-1,    //圈子类型0免费，1付费 -1全部
	        "type":-1,       //圈子类型0初级小白1网赚项目2自媒体3项目投资4淘宝电商5微商6淘宝客7网红8广告策划9经营管理10流量玩家11站长12域名13网络技术14同城交流
	        "name":"",       //圈子名称查询字段
	        "username":"",    //创建者用户名搜索字段
	        "fk_user_id":user.userid,  //查询者id
	        'status':1,
	        "pageIndex":start,
	        "pageSize":per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/quanzi_list.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
  	},
	hrefTo(type,val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
  		}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
Vue.filter('typeFormatter',function (value){
	if(value==1){
		return "类目1";
	}else if(value==2){
		return "类目2";
	}else if(value==3){
		return "类目3";
	}
});

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.rightBox{width:35%;}
	.leftBox{width:50%;}
	.listBox li .listTitle{height: auto;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
	.addcircle{padding: 5px 10px;border: 1px solid #D51122; border-radius: 5px;color: #D51122;margin-top:32px;display: inline-block;}
</style>



