<template>
  <div class="addtHtml">
  	<van-cell-group>
	    <van-field v-model="title"  label="圈子名称：" placeholder="请输入" required/>
	</van-cell-group>
  	<div class="addList">
  		<h3>
  			<span style="color:red;">*</span>
  			<span class="inputLeft">圈子内容:</span>
  		</h3>
  		<textarea v-model="excerpt" id="ksjkds" type="" placeholder="请输入"></textarea>
  	</div>
  	<div class="addList">
  		<h3 class="imgH3" data-id="">
  			<span style="color:red;">*</span>
  			<span class="inputLeft">封面:</span>
  		</h3>
  		<van-uploader :after-read="onRead">
  			<div class="butIcons">选择封面</div>
		</van-uploader>
  	</div>
  	<br />
  	<div class="butAdd but-80-red" v-on:click="add">提交</div>
  </div>
</template>

<script>
$("title").text("上传图片");
export default {
  name: 'addtHtml',
  props: ['value'],
  data () {
    return {
    		title:'',
    		excerpt: '',
    }
  },
  created:function(){
  	
    
  },
  methods: {
  	onRead(file) {
      console.log(file)
      if(!file.file.name){return false;}
      var _this = this;
       $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[0].files[0]);
		$.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/guiboweb_editpic.php',
	        data:formData,
	        dataType:"json",
	        cache: false,
	        contentType: false,        /*不可缺*/
	        processData: false,        /*不可缺*/
	        success:function(res){
	        		console.log(res)
	        		$("#msgLoding").hide();
	        		if(res.path){
		    			$(".butIcons").hide();
		    			$(".imgH3").attr("data-id",_this.imgUrl+res.path);
		    			$(".van-uploader:after").css("content","")
		    			var img = $(".van-uploader img").attr("src");
				    if(img){
				      	$(".van-uploader img").attr("src",file.content);
				    }else{
				      	var imgHtml = '<img class="img" src="'+file.content+'" style="width:24rem;height:16rem; display: block;margin:10px auto;">'
				      	$(".van-uploader").append(imgHtml)
				    }
	        		}else{
	        			//_this.utilMsg(res.path);
	        		}
	        },
	        error : function(XmlHttpRequest, textStatus, errorThrown) {
	        },
	        complete : function(XMLHttpRequest, textStatus) {
	        }
	    });
		$(".imgH3").click(function(){
			
		})
    },
    add: function (event) {
    		var _this = this;
    		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
    		var content = this.excerpt;
    		var title = this.title;
    		var titlepic = $(".imgH3").attr("data-id");
    		if(!title){
    			_this.utilMsg("请填写圈子名称");
    			return false;
    		}else if(!content){
    			_this.utilMsg("请填写圈子内容");
    			return false;
    		}else if(!titlepic){
    			_this.utilMsg("请上传封面");
    			return false;
    		}
    		var datas = {
    			'fk_user_id':user.userid,
			'fufei_type':1,
			'type':0,
			'price':123.32,
			'name':title,
			'content':content,
			'head_img_url':titlepic
	    };
    		$("#msgLoding").show();
    		$.post(_this.apiUrl+'/e/wxapi/fabu_qz.php',datas,function(res){
    			console.log(res)
			$("#msgLoding").hide();
			if(res.status==1){
				_this.utilMsg("发布成功")
				 //ios跳转页面
				if(_this.utilIsLocalhost()==false){
					var content = {"type":"hrefTo","link":'/circle/circle',"title":"圈子"}
				    	var data = JSON.stringify(content);
				    ttf.nslog(data)
			  	}else{
			  		_this.$router.push('/circle/circle');
		  		}
			}else{
				_this.utilMsg(res.content)
				
			}
			 
		},"json")
    }
  }
}

</script>
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px 10px 5px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #999;z-index: 1;border: dashed 1px #c7c7c7;
	}
</style>



