<template>
  <div class="addtHtml">
  	<div class="addList">
  		<textarea v-model="excerpt" placeholder="发现新鲜事..."></textarea>
  		<div class="initBox oh imgBox">
  			<!--<div class="imgBoxLi">
  				<div class="del">X</div>
  				<img src="../../assets/chushouwangdian@2x.png"/>
  			</div>-->
  			<van-uploader :after-read="onRead">+</van-uploader>
  		</div>
  		
  	</div>
  	<div class="addList">
	  	<van-radio-group v-model="radio">
		  <van-cell-group>
		    <van-cell><van-radio name="0">仅好友可见</van-radio></van-cell>
		    <van-cell><van-radio name="1">全部圈子可见</van-radio></van-cell>
		  </van-cell-group>
		</van-radio-group>
	</div>
  	<div class="butAdd but-80-red" v-on:click="add">提交</div>
  </div>
</template>
<script>
$("title").text("上传图片");
var imgBoxLiCss = 'width:100px;height: 100px;margin:0 5px 10px 5px; text-align: center;position: relative;';
var delCss = 'position: absolute;top: 2px;right: 2px;width: 20px;height: 20px;text-align: center;';
export default {
  name: 'addtHtml',
  props: ['value'],
  data () {
    return {
    		title:'',
    		excerpt: '',
    		radio:'0',
    }
  },
  created:function(){
  	
    
  },
  methods: {
  	onRead(file) {
  		var _this = this;
  		var imgLength = $(".imgBoxLi").length;
  		if(imgLength>=9){
  			_this.utilMsg('最多上传9张');
  			return false;
  		}
      console.log(file)
      if(!file.file.name){return false;}
       $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[0].files[0]);
		$.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/guiboweb_editpic.php',
	        data:formData,
	        dataType:"json",
	        cache: false,
	        contentType: false,        /*不可缺*/
	        processData: false,        /*不可缺*/
	        success:function(res){
	        		console.log(res)
	        		$("#msgLoding").hide();
	        		if(res.path){
	        			var imgHtml = '<div class="imgBoxLi imgBoxLi'+imgLength+'" style="'+imgBoxLiCss+'"><div class="del"  style="'+delCss+'">X</div><img src="'+_this.imgUrl+res.path+'" width="100" height="100"/></div>'
	        			$(".imgBox .van-uploader").before(imgHtml);
	        			$(".imgBoxLi"+imgLength+" .del").click(function(){
						$(".imgBoxLi"+imgLength).remove();
						$(".imgBox .van-uploader").show();
					})
	        			if(imgLength==8){
	        				$(".imgBox .van-uploader").hide();
	        			}
	        		}else{
	        			//_this.utilMsg(res.path);
	        		}
	        }
	    });
		
    },
    add: function (event) {
    		var _this = this;
    		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
    		var content = this.excerpt;
    		var title = this.title;
    		var titlepic = [];
    		var imgBoxLi = $(".imgBoxLi img").length;
    		if(!content){
    			_this.utilMsg("请填写圈子内容");
    			return false;
    		}
    		if(imgBoxLi>0){
    			$(".imgBoxLi img").each(function(e){
    				var img = $(".imgBoxLi img").eq(e).attr("src");
	    			titlepic[e] = $(".imgBoxLi img").eq(e).attr("src");
	    		})
    		}else{
    			_this.utilMsg("请上传封面");
    			return false;
    		}
    		var datas = {
    			'userid':user.userid,
			'text_content':content,
			'img_content':JSON.stringify(titlepic),
			'fk_qz_id':_this.$route.params.id,
			"who_can_see":_this.radio
	    };
    		$("#msgLoding").show();
    		$.post(_this.apiUrl+'/e/wxapi/fabu_dt.php',datas,function(res){
    			console.log(res)
			$("#msgLoding").hide();
			if(res.status==1){
				_this.utilMsg("发布成功")
				 //ios跳转页面
				if(_this.utilIsLocalhost()==false){
					var content = {"type":"hrefTo","link":'/circle/circleDetail/'+_this.$route.params.id,"title":"圈子"}
				    	var data = JSON.stringify(content);
				    ttf.nslog(data)
			  	}else{
			  		_this.$router.push('/circle/circleDetail/'+_this.$route.params.id);
		  		}
			}else{
				_this.utilMsg(res.content)
				
			}
			 
		},"json")
    }
  }
}

</script>
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px 10px 5px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{
		border: dashed 1px #ccc;color: #ccc;font-size:80px;display: inline-block;text-align:center;
		width: 98px;height: 98px;margin-left: 5px;
		line-height:100px;
	}
	.van-uploader input{padding: 0;}
	.imgBox{display: flex;display: -webkit-flex; flex-wrap:wrap;width: 330px;margin: 0 auto;}
	.imgBoxLi{width:100px;height: 100px;margin-right: 10px; text-align: center;position: relative;}
	.imgBoxLi .del{position: absolute;top: 2px;right: 2px;width: 20px;height: 20px;text-align: center;}
	
</style>



