<template>
  <div class="contentHtml">
  	  <ul class="followBox">
  	  	<template v-for="(listData, key) in listDatas">
  	  		<li>
  	  			<div class="imgDiv"><img src="../../assets/chushouwangdian@3x.png"></div>
  	  			<div class="userinfo">
  	  				<div class="initTitle">{{listData.title}}</div>
  	  				<div class="color6 font14 text-oh1">{{listData.ask_content}}</div>
  	  			</div>
  	  			<div class="followDiv" v-bind:class="'follow'+key">
  	  				<span class="follow" v-bind:class="{ followOn: listData.id }" v-on:click="follow(listData.id ,key)">关注</span>
  	  			</div>
  	  		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>
<script>
$("title").text("问答");
export default {
  name: 'contentHtml',
  data () {
    return {
    		listDatas:[],
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		var user_id = '';
  	 	}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var start = _this.listDatas.length;
    		var per_page = 10;
    		var datas = {
    			"is_hot":-1,
    			"fk_user_id":user_id,
    			"pageSize":per_page,
    			"pageIndex":0,
    			"title":'',
    			"status":start,
    		}
	  	$.get(_this.apiUrl+'/e/wxapi/ask_list.php',datas,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		      _this.listDatas[start+i].key = start+i;
		   }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(val){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":"回答详情"}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
  		}
	},
  	follow(lid ,key){
  		var _this = this;
    		$("#msgLoding").show();
    		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg('请先登录');
  	 		return false;
  	 	}
    		var bgz_user_id = _this.askInfo.fk_user_id;
    		var data = {"fk_user_id":user_id,"bgz_user_id":bgz_user_id};
    		$.get(_this.apiUrl+'/e/wxapi/guanzhu.php',data,function(res){
			$("#msgLoding").hide();
			if(_this.askInfo.is_guanzhu){
    				$(".follow"+key).text("关注");
    				_this.utilMsg('已取消关注');
    				_this.askInfo.is_guanzhu = false;
    			}else{
    				$(".follow"+key).text("已关注");
    				_this.utilMsg('关注成功')
    				_this.askInfo.is_guanzhu = true;
    			}
		},'json')
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.followBox{}
	.followBox li{margin-bottom: 10px;background: #fff;overflow: hidden;padding: 15px;}
	.followBox li .imgDiv{width: 15%;float: left;}
	.followBox li .imgDiv img{width: 40px;height: 40px;border-radius: 50%;-webkit-border-radius: 50%;}
	.followBox li .userinfo{width: 60%;float: left;}
	.followDiv{margin-top:10px;width: 20%;float: right;}
	.follow{float: right;padding: 6px 10px;color: #ff1919;border: 1px solid #ff1919;border-radius: 5px;-webkit-border-radius: 5px;}
	.followOn{color: #ccc;border: 1px solid #ccc;}
	.initTitle{margin-bottom: 5px;}
</style>



