<template>
  <div class="contentHtml">
  	  <ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo('/index/classroom/classroomDetail/'+listData.id)">
	  			<div class="rightBox"><img v-bind:src="listData.lesson_head_img"></div>
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div><img src="../../../assets/wujiaoxingbianse@3x.png" class="textImg" width="20" height="20">直播中</div>
	    				<div class="listInfo">
	    					<img v-bind:src="listData.lesson_head_img" class="textImg" width="20" height="20">
	    					<span>{{listData.username}}&emsp;</span>
	    					<span class="fr">{{listData.focus_num}}人｜{{isType}}</span>
	    				</div>
	    			</div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("课堂类型");
var apihost = '';
export default {
  name: 'contentHtml',
  data () {
    return {
	    	listDatas:[],
	    	input:'',
	    	message:'',
	    	this_page:1,
	    	isType:'免费'
    }
  },
  created:function(){
  	var _this = this;
  	apihost = _this.apiUrl;
  	var theId = _this.$route.params.id;
  	if(theId==0){
  		_this.isType = '免费';
  	}else if(theId==1){
  		_this.isType = '付费';
  	}else if(theId==2){
  		_this.isType = '热门';
  	}else if(theId==3){
  		_this.isType = '已报';
  	}
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		getList();
	  		return false;
	  	}
	  }
	})
	getList();
	function getList() {
    		$(".lodingDiv").text('').addClass("loding").show();
    		var per_page = 20;
    		var start = _this.listDatas.length;
    		var typeNum = -1;
	  	var is_hot = -1;
	  	var fk_user_id = '';
    		if(theId==0){
	  		_this.isType = '免费';
	  		var typeNum = 0;
	  	}else if(theId==1){
	  		_this.isType = '付费';
	  		var typeNum = 1;
	  	}else if(theId==2){
	  		_this.isType = '热门';
	  		is_hot = 0;
	  	}else if(theId==3){
	  		_this.isType = '已报';
	  		if(localStorage.user){
	    			fk_user_id = JSON.parse(localStorage.user).userid;
	    		}else{
	    			_this.utilMsg("请先登录");
	    			return false;
	    		}
	  	}
    		var datas = {
    			"fk_user_id":fk_user_id,
	        "username":"",
			"type":typeNum,
			"is_hot":is_hot,
			"end_time":"",
			"status":1,
			"title":"",
			"pageIndex":start,
			"pageSize":per_page,
		}
	  	$.get(_this.apiUrl+'/e/wxapi/lesson_list.php',datas,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},"json")
    }
  },
  methods: {
	hrefTo(val){//ios跳转页面
		console.log(this.utilIsLocalhost())
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":"课堂详情"}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
	  	}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
Vue.filter('imgFormatter',function (value){
	return apihost+value
});

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.listBox li{justify-content:flex-start;}
	.rightBox{margin-right: 10px;}
	.listBox li .listTitle{font-size: 16px;height: 40px;}
	.leftBox .listInfo img{border-radius: 50%;-webkit-border-radius: 50%;}
</style>



