<template>
  <div class="contentHtml">
  	<div class="initBox mb10">
  		<div class="initTitle">{{jbDetail.title}}</div>
  		<div>预算价格：<span class="colorA">¥{{jbDetail.budget_start}}-{{jbDetail.budget_end}}</span></div>
  		<div>订单编号：<span>{{jbDetail.content}}</span></div>
  		<div class="flex"><span>发布时间：{{jbDetail.time}}</span><span>可预价</span></div>
  		<div class="flex"><span>有效期限：{{jbDetail.stop_time}}</span><span>{{jbDetail.buynum}}参与</span></div>
  	</div>
  	<div class="initBox color6 mb10">
  		<div>{{jbDetail.content}}</div>
  		<div>附件：<a style="color: #00A5E7;" href="###">文件.doc</a></div>
  	</div>
  	<div class="initBox mb10">
  		<div>补充需求：</div>
  		<div>
  			<div>{{jbDetail.tips_1}}</div>
  			<div class="color9">{{jbDetail.tips_1_time}}</div>
  		</div>
  		<div>
  			<div>{{jbDetail.tips_2}}</div>
  			<div class="color9">{{jbDetail.tips_2_time}}</div>
  		</div>
  	</div>
  	<div class="initBox">
  		<div class="initTitle text-c mb10">———— 服务商报价 ————</div>
  		<ul>
  			<template v-for="listData in listDatas">
	  			<li class="mb10 initBox lineHeight">
		  			<div class="flexLeft line">
			  			<div>
			  				<img src="../../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
			  				<div><van-tag plain type="danger">联系</van-tag></div>
			  			</div>
			  			<div class="color9">
			  				<div class="initTitle">{{listData.sname}}</div>
			  				<div>报价：<span class="colorA">¥{{listData.price}}</span></div>
			  				<div class="color3">{{listData.service_summary}}</div>
			  				<div><span>编号：{{listData.order_num}}</span></div>
			  				<div class="mb10" v-on:click="zhongbiao(listData.id)"><van-tag type="danger">中标</van-tag></div>
			  			</div>
		  			</div>
		  			<div class="flex"><span>{{listData.sname}}</span><span style="color: #00A5E7;">评论</span></div>
		    		</li>
	    		</template>
	    	</ul>
  	</div>
  	<div class="lodingDiv loding"></div>
  	<div class="butBottom" v-on:click="canyujb">参与报价</div>
  </div>
</template>

<script>
$("title").text("出售网店");
import { Dialog } from 'vant';
export default {
  name: 'contentHtml',
  data () {
    return {
    		jbDetail:'',
    		listDatas:[]
    }
  },
  created:function(){
  	var _this=this;
  	//获取课堂详情
  	$.get(_this.apiUrl+'/e/wxapi/get_jb_by_id.php',{"jbid":_this.$route.params.id},function(res){
  		console.log(res)
  		_this.jbDetail = res[0];
	},"json");
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  	
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 10;
    		var obj = {
			"order_id":_this.$route.params.id,
			"pageIndex":start,
			"pageSize":per_page
	    };
	  	$.post(_this.apiUrl+'/e/wxapi/canyujb_list.php',obj,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},"json")
  	},
  	//ios跳转页面
	hrefTo:function(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title,}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
  		}
	},
	isZhongbiao:function(id){
		if(this.jbDetail.nstatus == 4){
			return true;
		}else{
			return false;
		}
	},
  	//中标
	zhongbiao:function(id){
		var _this=this;
		//是否已经中标
		if(this.isZhongbiao()){
			_this.utilMsg("该竞标已经结束")
			return false;
		}
		var getUser = localStorage.user;
		if(getUser){
  	 		var user_id =  JSON.parse(getUser).userid;
  	 	}else{
  	 		_this.utilMsg("请先登录");
  	 		return false;
  	 	}
		if(user_id== _this.jbDetail.fk_user_id){
			Dialog.confirm({
			  title: '',
			  message: '确认选择改项位中标？',
			  confirmButtonText:'立即付款',
			  cancelButtonText:'考虑考虑'
			}).then(() => {
			    // on confirm
				var obj = {
					"ddid":id,  //订单id
			        "is_ctk":-1,  //是否为充值提现退款业务 -1不是1是
			        //is_buy_again:0,  //是否再次购买0否，1是
			        "czcoin":-1,    //充值金额   //如果不是充值业务则为-1
			        "txcoin":-1,     //提现金额   //如果不是提现业务则为-1
			        "tkcoin":-1      //退款   //如果不是退款业务则为-1
				};
				$("#msgLoding").show();
				$.post(_this.apiUrl+'/e/wxapi/fukuan.php',obj,function(res){
					console.log(res);
		  			$("#msgLoding").hide();
		  			if(res.status==true){
		  				_this.jbDetail.nstatus = 4;
		  			}
		  			_this.utilMsg(res.content);
				},"json")
			})
		}
	},
	//参与报价
	canyujb:function(){
		var _this=this;
		//是否已经中标
		if(this.isZhongbiao()){
			_this.utilMsg("该竞标已经结束")
			return false;
		}
		var getUser = localStorage.user;
		if(getUser){
  	 		var user_id =  JSON.parse(getUser).userid;
  	 	}else{
  	 		_this.utilMsg("请先登录");
  	 		return false;
  	 	}
		var datas = {
	        "userid":user_id,
			"order_id":_this.$route.params.id
	    };
	    $("#msgLoding").show();
	  	//获取课堂详情
	  	$.post(_this.apiUrl+'/e/wxapi/canyujb.php',datas,function(res){
	  		console.log(res)
	  		$("#msgLoding").hide();
	  		_this.hrefTo('/index/bidding/canyujb/'+res.id,'填写报价');
		},"json")
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
	.butBottom{
		font-size: 16px; text-align: center;color: #fff;background: #ff1919;width: 100%;
		line-height: 50px;position: fixed;right: 0;bottom: 0;
	}
</style>
	
</style>



