<template>
  <div class="contentHtml">
  	  <div class="detailBox">
	  	<div class="title">{{askInfo.title}}</div>
	  	<div class="idInfo" >
	  		<img class="fl imgBr" src="../../../../build/logo.png" width="40" height="40"/>
	  		<span class="fl" style="margin-left: 10px;">
	  			<span>{{askInfo.username}}</span><br/>
	  			<span class="color9" style="line-height: 2.5rem;">{{askInfo.time}}</span>
	  		</span>
	  		<span class="follow" v-bind:class="{ followOn: askInfo.is_guanzhu }" v-on:click="follow">关注</span>
	  	</div>
	  	<div class="content">{{askInfo.ask_content}}</div>
	  	<div style="color: #999;font-size: 1.2rem;margin: 10px 0;">阅读：{{askInfo.onclick}}<span v-on:click="fabulous(askInfo.id)" class="fabulous" v-bind:class="{ fabulousOn: askInfo.is_zhan }">{{askInfo.zhan_count}}</span></div>
  	</div>
  	<div class="initBox">
  		<div><span style="border-bottom: 2px solid #ff1919;font-size:16px;line-height:40px;padding-bottom: 5px;">全部{{listDataCount}}个回答</span></div>
  		<br />
  		<ul>
  			<template v-for="listData in listDatas">
	  			<li class="mb10 initBox lineHeight line" v-bind:class="'delete'+listData.id">
		  			<div class="flexLeft" >
			  			<div class="width15">
			  				<img src="../../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
			  			</div>
			  			<div class="color9 width85">
			  				<div class="flex"><span>{{listData.username}}</span><span v-on:click="plFabulous(listData.id,listData.key)" class="fabulous" v-bind:class="{ fabulousOn: listData.is_zhan }">{{listData.zhan_count}}</span></div>
			  				<div class="color3">{{listData.pl_content}}</div>
			  				<div class="flex"><span>{{listData.time}}</span><span class="colorA" v-bind:class="{ hideCss: listData.hideDelete }" v-on:click="deletes(listData.id)">删除</span></div>
			  			</div>
		  			</div>
		    		</li>
	    		</template>
	   </ul>
  	</div>
  	<div class="lodingDiv loding"></div>
  	<div class="bottomNav">
  		<input v-model="value" type="text" placeholder="输入评论内容"/>
  		<img v-on:click="transmission" src="../../../assets/pinglun@3x.png" width="20" height="20"/>
  		<!--<img v-on:click="collection" src="../assets/shoucang@3x.png" width="20" height="20"/>-->
  		<span class="fabulous" v-bind:class="{ fabulousOn: askInfo.is_zhan }" v-on:click="fabulous(askInfo.id)" style="height: 20px;width: 20px;"/></span>
  	</div>
  </div>
</template>
<script>
$("title").text("问题详情");
export default {
  name: 'contentHtml',
  data () {
    return {
    		askInfo:"",
    		value:"",
    		isFollow:false,
    		isFabulou:false,
    		isPlFabulous:false,
    		listDatas:[],
    		listDataCount:0
    }
  },
  created:function(){
  	var _this = this;
  	if(localStorage.user){
 		var user_id =  JSON.parse(localStorage.user).userid;
 	}else{
 		var user_id = '';
 	}
  	$.get(_this.apiUrl+'/e/wxapi/get_ask_by_id.php',{"itemid":_this.$route.params.id,"fk_user_id":user_id},function(res){
  		console.log(res);
  		_this.askInfo = res[0];
	},'json')
  	
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  	
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		if(localStorage.user){
	 		var user_id =  JSON.parse(localStorage.user).userid;
	 	}else{
	 		var user_id = '';
	 	}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var start = _this.listDatas.length;
    		var per_page = 10;
    		var datas = {
    			"fk_user_id":user_id,
    			"fk_answer_id":_this.$route.params.id,
	        "pageIndex":start,
	        "pageSize":per_page
    		}
	  	$.post(_this.apiUrl+'/e/wxapi/answer_pl_list.php',datas,function(res){
	  		console.log(res)
	  		$(".lodingDiv").hide();
	  		_this.listDataCount = res.count.pl_count;
			for(var i = 0; i < res.data.length; i++) {
		      _this.listDatas[start+i] = res.data[i];
		      _this.listDatas[start+i].key = start+i;
		      if(user_id ==res.data[i].fk_user_id||user_id ==res.data[i].fk_user_id1){
			      _this.listDatas[start+i].hideDelete = false;
			  }else{
			  	_this.listDatas[start+i].hideDelete = true;
			  }
			}
			_this.$forceUpdate();
			if(res.data.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
  	follow(){
  		var _this = this;
    		$("#msgLoding").show();
    		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg('请先登录');
  	 		return false;
  	 	}
    		var bgz_user_id = _this.askInfo.fk_user_id;
    		var data = {"fk_user_id":user_id,"bgz_user_id":bgz_user_id};
    		$.get(_this.apiUrl+'/e/wxapi/guanzhu.php',data,function(res){
			$("#msgLoding").hide();
			if(_this.askInfo.is_guanzhu){
    				$(".follow").text("关注");
    				_this.utilMsg('已取消关注');
    				_this.askInfo.is_guanzhu = false;
    			}else{
    				$(".follow").text("已关注");
    				_this.utilMsg('关注成功')
    				_this.askInfo.is_guanzhu = true;
    			}
		},'json')
  	},
  	deletes:function(id){
  		var _this = this;
    		$("#msgLoding").show();
    		var data = {'pl_id':id};
    		$.get(_this.apiUrl+'/e/wxapi/del_answer_pl.php',data,function(res){
    			$("#msgLoding").hide();
    			if(res.status == 1){
    				$(".delete"+id).remove();
    			}else{
    				_this.utilMsg(res.content);
    			}
		},'json')
  	},
  	//评论
  	transmission(){
  		var _this = this;
    		var user = localStorage.user;
    		if(!user){
    			_this.utilMsg("请先登录")
    			return false;
    		}
    		user = JSON.parse(user);
    		var content = _this.value;
    		if(content==''){
    			_this.utilMsg("内容为空")
    			return false;
    		}
    		$("#msgLoding").show();
    		var datas = {
	        "fk_user_id":user.userid,
	        "fk_answer_id":_this.$route.params.id,
	        "pl_content":content
	    };
    		$.post(_this.apiUrl+'/e/wxapi/add_answer_pl.php',datas,function(res){
			console.log(res)
			$("#msgLoding").hide();
			_this.utilMsg('评论成功');
			_this.listDatas = [];
			_this.getList();
		},"json")
  	},
  	//点赞
    fabulous: function (id) {
    		var _this = this;
    		var user = localStorage.user;
  		if(!user){
  			_this.utilMsg('请先登录')
  			return false;
  		}
  		user = JSON.parse(localStorage.user);
    		$("#msgLoding").show();
    		var data = {
    			"fk_ask_id":id,
			"fk_user_id":user.userid
    		};
    		$.get(_this.apiUrl+'/e/wxapi/ask_zhan.php',data,function(res){
	  		$("#msgLoding").hide();
    			if(_this.askInfo.is_zhan){
    				_this.utilMsg('已取消点赞');
    				_this.askInfo.is_zhan = false;
    				_this.askInfo.zhan_count = _this.askInfo.zhan_count*1-1;
    			}else{
    				_this.utilMsg('点赞成功')
    				_this.askInfo.is_zhan = true;
    				_this.askInfo.zhan_count = _this.askInfo.zhan_count*1+1;
    			}
    			_this.$forceUpdate();
		},'json')
    },
    //评论点赞
    plFabulous: function (id,key) {
    		var _this = this;
    		var user = localStorage.user;
  		if(!user){
  			_this.utilMsg('请先登录')
  			return false;
  		}
  		user = JSON.parse(localStorage.user);
    		$("#msgLoding").show();
    		var data = {
    			"fk_ans_id":id,
			"fk_user_id":user.userid
    		};
    		$.get(_this.apiUrl+'/e/wxapi/ans_zan.php',data,function(res){
	  		$("#msgLoding").hide();
    			if(_this.listDatas[key].is_zhan){
    				_this.utilMsg('已取消点赞');
    				_this.listDatas[key].is_zhan = false;
    				_this.listDatas[key].zhan_count = _this.listDatas[key].zhan_count*1-1;
    			}else{
    				_this.utilMsg('点赞成功')
    				_this.listDatas[key].is_zhan = true;
    				_this.listDatas[key].zhan_count = _this.listDatas[key].zhan_count*1+1;
    			}
    			_this.$forceUpdate();
		},'json')
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.fabulous{float: right;background: url(../../../assets/dadianzan@3x.png)left no-repeat;background-size: 1.5rem 1.5rem;padding-left:2rem;font-size: 1.2rem;}
	.fabulousOn{background: url(../../../assets/dadianzanbianse@3x.png)left no-repeat;background-size: 1.5rem 1.5rem;}
	.subTite{line-height:50px;border-bottom: 1px solid #f7f7f7;text-align: center;}
	.detailBox{background: #fff;margin-bottom: 10px;padding: 10px;}
	.detailBox .title{font-size: 1.5rem;color: #333;}
	.detailBox .idInfo{overflow: hidden;margin: 30px 0;}
	.detailBox .idInfo .follow{float: right;padding: 6px 10px;color: #ff1919;border: 1px solid #ff1919;border-radius: 5px;-webkit-border-radius: 5px;}
	.detailBox .idInfo .followOn{color: #ccc;border: 1px solid #ccc;}
	
	.detailBox .content{font-size: 1.2rem;color: #666;}
	.shareBox .title{line-height:50px;border-bottom: 1px solid #f7f7f7;}
	.shareBox .iconBox{display:flex;display:-webkit-flex;justify-content:space-around;padding: 10px 0;}
	
	.commintBox li{padding: 10px 0;border-bottom: 1px solid #f7f7f7;}
	.commintBox li .authorInfo{color: #999;overflow: hidden;margin-bottom: 10px;}
	.commintBox li .text{margin-left:40px ;font-size: 1.5rem;}
	.bottomNav{
		height:30px;position: fixed;bottom: 0;left: ;width: 100%;
		background: #f7f7f7;display:flex;display:-webkit-flex;
		justify-content:space-around;padding:10px 0;
	}
	.bottomNav input{
		width:17rem;line-height: 2rem;height: 2rem;
		border-radius:100px;-webkit-border-radius:100px;
	}
	.lodingDiv{background: #fff;}
	.hideCss{display: none;}
</style>



