<template>
  <div class="contentHtml">
	   <ul>
	  		<template v-for="listData in listDatas">
	  			<li class="mb10 initBox lineHeight line" v-on:click="hrefTo('/user/order/orderDetail/'+listData.id,'订单详情')">
	  				<div class="line oh">订单号: {{listData.order_num}}<span class="fr colorA">{{listData.nstatus | orderType}}</span></div>
		  			<div style="padding: 5px 0;">{{listData.title}}</div>
		  			<div><span class="colorA">¥{{listData.price}}</span><span class="fr color9">类目:{{listData.jiahuo_illegal}}</span></div>
		  		</li>
	    	  </template>
	   </ul>
	   <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("我的订单");
export default {
  name: 'contentHtml',
  data () {
    return {
    		listDatas:[],
    }
  },
  created:function(){
  	this.getList();
  },
  methods: {
    getList:function(){
    	
    		var _this = this;
	  	if(localStorage.user){
			var fk_user_id = JSON.parse(localStorage.user).userid;
		}else{
			_this.utilMsg("请先登录");
			return false;
		}
		var start = _this.listDatas.length;
		var per_page = 10;
  		var obj = {
	        "fk_user_id":-1,
			'order_num':'',
			'order_time':'',
	        'username': '',
	        "pageIndex":start,
			"pageSize":per_page,
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/jbdd_list.php',obj,function(res){
		  	$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
				_this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
  	},
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	    }else{
	  		this.$router.push(val)
  		}
	}
  }
}

</script>
<style scoped>
	
</style>



