
<template>
  <div class="contentHtml">
  	  <van-cell-group>
	  	<van-cell title="订单编号"  v-bind:value="orderDetail.order_num" />
	  	<van-cell title="下单时间"  v-bind:value="orderDetail.public_time" />
	  	<van-cell title="网店标题"  v-bind:value="orderDetail.title" />
	  	<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">出售价格</span>
		    </template>
		    <span>¥{{orderDetail.price}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">消费者保证金</span>
		    </template>
		    <span>¥{{orderDetail.bond}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">技术年费</span>
		    </template>
		    <span>¥{{orderDetail.technical_annual_fee}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">手续费</span>
		    </template>
		    <span>¥{{orderDetail.technical_annual_fee}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">订单总额</span>
		    </template>
		    <span>¥{{(orderDetail.price*1)+(orderDetail.technical_annual_fee)*1}}</span>
  		</van-cell>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	  <van-cell-group>
	  	<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">当前账户可使用余额：</span>
		    </template>
		    <span>¥{{orderDetail.money}}</span>
		    <span>立即充值</span>
  		</van-cell>
	  	<van-cell>
	    	  <template slot="title">
	        <span class="van-cell-text">付款方式</span>
	      </template>
	      <van-radio name="1" v-model="radio">支付全款</van-radio>&nbsp;&nbsp;
	      <van-radio name="2" v-model="radio">支付定金</van-radio>
	    </van-cell>
	  </van-cell-group>
	  <div style="line-height:40px;color: #999;">（如果么有设置支付密码，那么支付宝密码就是你得密码）</div>
	  <van-cell-group>
	  	<van-field  label="支付宝账号" placeholder="请输入" required/>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="onSubmit">确认支付</div>
	 </div>
  </div>
</template>

<script>
setTimeout(function(){
	$("title").text("订单信息");
},1)
export default {
  name: 'contentHtml',
  data () {
    return {
    		orderDetail:'',
    		radio:'1'
    }
  },
  created:function(){
  	var _this = this;
  	var obj = {"oid":_this.$route.params.id};
    $.ajax({
        type:'POST',
        url:_this.apiUrl+'/e/wxapi/orderitem.php',
        data:obj,
        success:function(result){
        		_this.orderDetail = JSON.parse(result)[0];
            console.log(_this.orderDetail);
        },
        error : function(XmlHttpRequest, textStatus, errorThrown) {
            _this.utilMsg(XmlHttpRequest);
        }
    });
  },
  methods: {
  	//付款
	onSubmit:function(id){
		var _this = this;
		if(!localStorage.user){
    			_this.utilMsg("请先登录");
    			return false;
    		}
		var obj = {
			"ddid":_this.$route.params.id,  //订单id
	        "is_ctk":-1,  //是否为充值提现退款业务 -1不是1是
	        //is_buy_again:0,  //是否再次购买0否，1是
	        "czcoin":-1,    //充值金额   //如果不是充值业务则为-1
	        "txcoin":-1,     //提现金额   //如果不是提现业务则为-1
	        "tkcoin":-1      //退款   //如果不是退款业务则为-1
		};
		$("#msgLoding").show();
	    $.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/fukuan.php',
	        data:obj,
	        success:function(result){
	        		$("#msgLoding").hide();
	        		_this.utilMsg("付款成功")
	            console.log(result);
	            if(_this.orderDetail.type1==4){
	            		_this.addToQuanzi();
	            }
	        },
	        error : function(XmlHttpRequest, textStatus, errorThrown) {
	            _this.utilMsg(XmlHttpRequest);
	        }
	    });
	},
	//ios跳转页面
	hrefTo:function(id){
		
	},
	//加入圈子
	addToQuanzi:function(id){
		var _this = this;
		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
		var data = {
			"fk_quanzi_id":_this.orderDetail.order_id,//圈子id
			"fk_user_id":user.userid,  //查询者id
	   };
	    $("#msgLoding").show();
	  	$.get(_this.apiUrl+'/e/wxapi/add_to_quanzi.php',data,function(res){
	  		$("#msgLoding").hide();
	  		_this.utilMsg('加入成功');
	  		if(_this.utilIsLocalhost()==false){
				var content = {"type":"hrefTo","link":"/circle/circle","title":'圈子'}
			    	var data = JSON.stringify(content);
			    ttf.nslog(data)
		  	}else{
		  		_this.$router.push("/circle/circle");
	  		}
		},'json')
	},
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>



