<template>
  <div class="contentHtml">
  	  <van-cell-group>
  	  	<van-field  label="课堂标题" v-model="title" placeholder="请输入" required/>
	    <div class="addList" style="padding-bottom:80px;">
			<h3 class="imgH3">
	  			<span style="color:red;">*</span>
	  			<span class="inputLeft">课堂简介:</span>
	  		</h3>
	  		<textarea v-model="excerpt" type="" placeholder="请输入"></textarea>
	    </div>
	    <van-cell title="课堂类型" is-link v-on:click="popShow('wangdian')" :value="wangdian.val"/>
	    <van-field v-model="price"  label="价格" type="number" placeholder="请输入" required/>
	  </van-cell-group>
	  
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="commintBut">提交审核</div>
	 </div>
	 <!--网店类型-->
	 <van-popup v-model="wangdian.type" position="bottom" :overlay="true">
		<van-picker :columns="wangdian.list" show-toolbar :title="wangdian.title" @cancel="onCancel()" @confirm="wangdianOnConfirm" /> 
	</van-popup>
  </div>
</template>

<script>
$("title").text("出售网店");
export default {
  name: 'contentHtml',
  data () {
    return {
    		excerpt:'',
    		money:"",
    		title:"",
    		price:'',
    		wangdian: {
    			"list":['免费', '付费'],
    			"title":"选择课堂类型",
    			"indexList":[0,1],
    			"val":"免费",
    			"index":0,
    			"type":false
    		}
    }
  },
  created:function(){
  	var _this = this;
  },
  methods: {
  	commintBut(){
  		var _this = this;
  		if(!_this.title){
  			_this.utilMsg("请填写标题")
  			return false;
  		}else if(!_this.excerpt){
  			_this.utilMsg("请填写描述")
  			return false;
  		}else if(!_this.price){
  			_this.utilMsg("请填写价格")
  			return false;
  		}
  		$("#msgLoding").show();
    		var datas = {
	        type:_this.wangdian.index,    //课堂类型，0免费，1付费
	        is_hot:1,  //是否热门级别0普通，1一级，2二级
	        end_time:"", //课堂结束时间
	        smalltext:_this.excerpt, //课堂简介
	        lesson_count:10,   //课程数
	        lesson_head_img:"http://121.14.27.27:8080/e/wxapi/image/20180312/1520869376.jpeg", //课程头图
	        title:_this.title
	    };
    		_this.utilAjax(_this.apiUrl+'/e/wxapi/addLesson.php',datas,function(res){
    			$("#msgLoding").hide();
    			_this.utilMsg("发布成功")
		},"post");
		
  		
  	},
    popShow: function (type) {
    		this.wangdian.type = true
  		this.bankCard = false;
  	},
  	wangdianOnConfirm(value, index) {
      this.wangdian.val = value;
      this.wangdian.index = this.wangdian.indexList[index];
      this.wangdian.type = false;
    },
    onCancel(type) {
    		this.wangdian.type = false;
    		this.timeBox.type = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 14px;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>
	
</style>



