<template>
  <div class="contentHtml">
  	<div class="typebox line">
  		<span v-bind:class="{ colorA: typeIndex == 0 }" v-on:click="typeIndexs(0)">找需求</span><span v-bind:class="{ colorA: typeIndex == 1 }" v-on:click="typeIndexs(1)">找服务商</span>
  	</div>
  	<div v-bind:class="{ hideDiv: typeIndex == 1 }">
		<ul class="iconType">
			<li>
				<img v-on:click="setIsType(1)" src="../../../assets/weiguikoufen@3x.png"/>
				<p>违规扣分</p>
			</li>
			<li>
				<img v-on:click="setIsType(2)" src="../../../assets/taobaoke@3x.png"/>
				<p>淘宝客</p>
			</li>
			<li>
				<img v-on:click="setIsType(3)" src="../../../assets/kefuwaibao@3x.png"/>
				<p>客服外包</p>
			</li>
			<li>
				<img v-on:click="setIsType(4)" src="../../../assets/maijiaxiu@3x.png"/>
				<p>买家秀</p>
			</li>
			<li>
				<img v-on:click="setIsType(5)" src="../../../assets/dazaobaokuan@3x.png"/>
				<p>打造爆款</p>
			</li>
			<li>
				<img v-on:click="setIsType(6)" src="../../../assets/mote@3x.png"/>
				<p>模特</p>
			</li>
			<li>
				<img v-on:click="setIsType(7)" src="../../../assets/daiyunying@3x.png"/>
				<p>代运营</p>
			</li>
			<li>
				<img v-on:click="setIsType(8)" src="../../../assets/sheying@3x.png"/>
				<p>摄影</p>
			</li>
			<li>
				<img v-on:click="setIsType(9)" src="../../../assets/xuqian@3x.png"/>
				<p>续签</p>
			</li>
			<li>
				<img v-on:click="setIsType(10)" src="../../../assets/wangdianzhuangxiu@3x.png"/>
				<p>网店装修</p>
			</li>
		</ul>
	  	<ul>
	  		<template v-for="listData in listDatas">
		  		<li class="mb10 initBox lineHeight">
		  			<div style="margin-bottom: 5px;" v-on:click="hrefTo('/index/bidding/biddingDetail/'+listData.id,'竞价详情')">
			  			<div class="initTitle">{{listData.title}}</div>
			  			<div class="color9">{{listData.content}}</div>
		  			</div>
		  			<div class="color9">
			  			<div class="flex"><span class="colorA">¥{{listData.price}}</span><span>{{listData.buynum}}人参与｜竞价</span></div>
			  			<div class="flex"><span>已托管</span><span>{{listData.stop_time}}截止</span></div>
		  			</div>
		    		</li>
	  		</template>
	     </ul>
     </div>
     <div v-bind:class="{ hideDiv: typeIndex == 0 }">
     	<div class="mb10"></div>
	  	<ul class="serviceList">
	  		<div class="noloding">未加载</div>
	  		<template v-for="listData in listDatas2">
		  		<li class="mb10 initBox lineHeight flexLeft" v-on:click="hrefTo('/index/bidding/biddingDetail/'+listData.id,'服务商详情')">
		  			<img src="../../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
		  			<div>
		  				<div class="initTitle">{{listData.sname}}</div>
		  				<div class="color9"><span style="width: 100px;display: inline-block;">文章：{{listData.wzs}}</span><span>课堂：{{listData.kcs}}</span></div>
		  				<div class="color9"><span style="width: 100px;display: inline-block;">问答：{{listData.wds}}</span><span>成交量：{{listData.dealcount}}</span></div>
		  				<div class="color9">{{listData.service_summary}}</div>
		  			</div>
		    		</li>
	    		</template>
	     </ul>
     </div>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("竞价");

export default {
  name: 'contentHtml',
  data () {
    return {
	    	typeIndex: 0,
	    	listDatas:[],
	    	listDatas2:[],
	    	input:'',
	    	message:'',
	    	this_page:1,
	    	isType:-1  //-1表示全部 需求类型1违规扣分2淘宝客3客服外包4买家秀5打造爆款6模特7代运营8摄影9续签10网店装修
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  },
  methods: {
  	setIsType:function(id){
  		this.isType = id;
  		this.listDatas = [];
  		this.getList();
  		
  	},
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 6;
    		var obj = {
			"username":'',
			"type": _this.isType,
			"title":"",
			"budget_end":-1,
			"num":'',
			"time":'',
			"is_top":-1,
			"status":-1,
			"examine":1,
			"pageIndex":start,
			"pageSize":per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/demand_list.php',obj,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},"json")
  	},
  	getList2:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas2.length;
    		var per_page = 6;
    		var obj = {
    			'username':'',
			'name':'',
			'is_exam':1,
			'pageIndex':start,
			'pageSize':per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/service_list.php',obj,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas2[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},"json")
  	},
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title,}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
  		}
	},
	typeIndexs(index){
		this.typeIndex = index;
		if(index==1){
			var noloding = $(".serviceList .noloding").text();
			if(noloding){
	    			this.getList2();
	    			$(".serviceList .noloding").remove();
	    		}
		}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.typebox{text-align: center;line-height:24px;padding: 20px 0; font-size: 16px;background: #fff;}
	.typebox span{width: 49%;display: inline-block;}
	.typebox span:first-child{border-right: 1px solid #CCCCCC;}
	.iconType{text-align: center;font-size:12px;background: #fff;margin-bottom:10px;overflow: hidden;}
	.iconType li{width: 20%;padding:15px 0;float: left;}
	.iconType li img{width:3rem;height: 3rem;}
	.hideDiv{display: none;}
</style>



