<template>
  <div class="contentHtml">
  	  <ul>
  	  	<template v-for="listData in listDatas">
	  		<li class="mb10 initBox lineHeight" >
	  			<div v-on:click="hrefTo('hrefTo','/index/answer/answerDetail/'+listData.id)">
		  			<div class="initTitle">{{listData.title}}</div>
	  				<div class="color6 font14 ">{{listData.ask_content}}</div>
  				</div>
	  			<div class="line1"></div>
	  			<div class="color9 flex">
	  				<span class="width33">关注人数:{{listData.ancount}}</span>
	  				<span v-on:click="hrefTo('hrefTo','/index/answer/answerDetail/'+listData.id)" class="width33 text-c">回答</span>
	  				<span v-on:click="fabulous(listData.id,listData.key)" class="fabulous width33 text-r" v-bind:class="{ fabulousOn: listData.is_zhan }"></span>
	  			</div>
	  		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>
<script>
$("title").text("问答");
export default {
  name: 'contentHtml',
  data () {
    return {
    	typeIndex: 0,
    	images: [
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg'
    ],
    	listDatas:[],
    	isFabulou:false,
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		var user_id = '';
  	 	}
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var start = _this.listDatas.length;
    		var per_page = 10;
    		var datas = {
    			"is_hot":-1,
    			"fk_user_id":user_id,
    			"pageSize":per_page,
    			"pageIndex":0,
    			"title":'',
    			"status":start,
    		}
	  	$.get(_this.apiUrl+'/e/wxapi/ask_list.php',datas,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		      _this.listDatas[start+i].key = start+i;
		   }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(type,val){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			if(type=="hrefTo"){
				var content = {"type":"hrefTo","link":val,"title":"回答详情"}
			}else{
				var content = {"type":"href","id":val}
			}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		if(type=="hrefTo"){
	  			this.$router.push(val)
	  		}else{
	  			this.$router.push('/index/bidding/biddingDetail/'+val)
	  		}
  		}
	},
	typeIndexs(index){
		this.typeIndex = index;
	},
	//点赞
    fabulous: function (id,key) {
    		var _this = this;
    		var user = localStorage.user;
  		if(!user){
  			_this.utilMsg('请先登录')
  			return false;
  		}
  		user = JSON.parse(localStorage.user);
    		$("#msgLoding").show();
    		var data = {
    			"fk_ask_id":id,
			"fk_user_id":user.userid
    		};
    		$.get(_this.apiUrl+'/e/wxapi/ask_zhan.php',data,function(res){
	  		$("#msgLoding").hide();
    			if(_this.listDatas[key].is_zhan){
    				_this.utilMsg('已取消点赞');
    				_this.listDatas[key].is_zhan = false;
    			}else{
    				_this.utilMsg('点赞成功')
    				_this.listDatas[key].is_zhan = true;
    			}
    			_this.$forceUpdate();
		},'json')
    },
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.fabulous{float: right;background: url(../../../assets/dadianzan@3x.png)right no-repeat;background-size: 1.5rem 1.5rem;padding-left:2rem;font-size: 1.2rem;}
	.fabulousOn{background: url(../../../assets/dadianzanbianse@3x.png)right no-repeat;background-size: 1.5rem 1.5rem;}
	
	.typebox{text-align: center;line-height:24px;padding: 20px 0; font-size: 16px;background: #fff;}
	.typebox span{width: 49%;display: inline-block;}
	.typebox span:first-child{border-right: 1px solid #CCCCCC;}
	.iconType{text-align: center;font-size:12px;background: #fff;margin-bottom:10px;overflow: hidden;}
	.iconType li{width: 16.66%;padding:15px 0;float: left;}
	.iconType li img{width:3rem;height: 3rem;}
	.hideDiv{display: none;}
	.line1{margin: 5px 0 ;border-bottom: 1px dashed #ebebeb;}
	.initTitle{margin-bottom: 5px;}
</style>



