<template>
  <div class="contentHtml">
  	<van-tabs @click="handleTabClick">
	  <van-tab v-for="titleText in titleObj" :title="titleText"></van-tab>
	  <div class="mb10"></div>
	  <!--课堂＝0-->
	  <ul :class="{ isshow : active==0 }">
	  		<template v-for="listData in listDatas.listDatas0">
	  			<li class="mb10 initBox lineHeight line" v-on:click="hrefTo('href','')">
		  			<div class="flex">
			  			<img src="../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
			  			<div class="color9">
			  				<div class="flex"><span>{{listData.bossname}}</span><span>成交量：100</span></div>
			  				<div class="color3">三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费</div>
			  				<div class="flex"><span>2018-03-23</span><span class="colorA">删除</span></div>
			  			</div>
		  			</div>
		    		</li>
	    		</template>
	    		
	    </ul>
	    <!--网店＝1-->
	    <ul :class="{ isshow : active==1 }">
	    		<div class="noloding">未加载</div>
	    		<template v-for="listData in listDatas.listDatas1">
	  			<li class="mb10 initBox lineHeight line" v-on:click="hrefTo('/user/order/orderDetail/'+listData.id1,'订单详情')">
	  				<div class="line oh">{{listData.order_num}}<span class="fr colorA">{{listData.nstatus | orderType}}</span></div>
		  			<div style="padding: 5px 0;">{{listData.title}}</div>
		  			<div><span class="colorA">¥{{listData.price}}</span><span class="fr color9">类目:{{listData.business  | businessType}}</span></div>
		  		</li>
	    		</template>
	    </ul>
	    <!--竞价＝3-->
	  <ul :class="{ isshow : active==2 }">
	  		<div class="noloding">未加载</div>
	  		<template v-for="listData in listDatas.listDatas2">
		    		<li class="mb10 initBox lineHeight line" v-on:click="hrefTo('href','')">
		  			<div class="flex">
			  			<img src="../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
			  			<div class="color9">
			  				<div class="flex"><span>会飞的老虎</span><span>成交量：100</span></div>
			  				<div class="color3">三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费</div>
			  				<div class="flex"><span>2018-03-23</span><span class="colorA">删除</span></div>
			  			</div>
		  			</div>
		    		</li>
	    		</template>
	   </ul>
	    <!--圈子＝5-->
	   <ul :class="{ isshow : active==3 }">
	   		<div class="noloding">未加载</div>
	   		<template v-for="listData in listDatas.listDatas3">
	  			<li class="mb10 initBox lineHeight line" v-on:click="hrefTo('href','')">
		  			<div class="flex">
			  			<img src="../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
			  			<div class="color9">
			  				<div class="flex"><span>6会飞的老虎</span><span>成交量：100</span></div>
			  				<div class="color3">三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费</div>
			  				<div class="flex"><span>2018-03-23</span><span class="colorA">删除</span></div>
			  			</div>
		  			</div>
		    		</li>
	    		</template>
	   </ul>
	   <div class="lodingDiv loding"></div>
	</van-tabs>
  </div>
</template>

<script>
$("title").text("我的订单");
export default {
  name: 'contentHtml',
  data () {
    return {
    		titleObj:["课堂","网店","竞价","圈子"],
    		active: 0,
    		listDatas:{
	    		'listDatas0':[],
	    		'listDatas1':[],
	    		'listDatas2':[],
	    		'listDatas3':[],
    		},
    }
  },
  created:function(){
  	this.getList(0);
  },
  methods: {
    handleTabClick(index) {
    		this.active = index;
    		var noloding = $(".contentHtml ul").eq(index).find(".noloding").text();
    		if(noloding){
    			this.getList(index);
    		}
    },
    getList:function(type){
    		var _this = this;
	  	if(localStorage.user){
			var fk_user_id = JSON.parse(localStorage.user).userid;
		}else{
			_this.utilMsg("请先登录");
			return false;
		}
		var active = type;
		if(active==0){
			var start = _this.listDatas.listDatas0.length;
		}else if(active==1){
			var start = _this.listDatas.listDatas1.length;
		}else if(active==2){
			var start = _this.listDatas.listDatas2.length;
		}else if(active==3){
			var start = _this.listDatas.listDatas3.length;
		}
		var per_page = 10;
  		var obj = {
	        "fk_user_id":fk_user_id,
	        "type":active,
	        "pageIndex":start,
			"pageSize":per_page,
	    };
	  	$.post(_this.apiUrl+'/e/wxapi/mjorderlist.php',obj,function(res){
		  	$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
				if(active==0){
					_this.listDatas.listDatas0[start+i] = res[i];
				}else if(active==1){
					_this.listDatas.listDatas1[start+i] = res[i];
				}else if(active==2){
					_this.listDatas.listDatas2[start+i] = res[i];
				}else if(active==3){
					_this.listDatas.listDatas3[start+i] = res[i];
				}
		    }
			$(".isshow .noloding").text('');
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		  	
		},'json')
  	},
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	    }else{
	  		this.$router.push(val)
  		}
	}
  }
}
import Vue from 'vue'
Vue.filter('orderType',function (value){
	var texts ='';
	if(value == 0){
		texts = '下单未付款'
	}else if(value == 1){
		texts = '已下单'
	}else if(value == 2){
		texts = '部分支付'
	}else if(value == 3){
		texts = '交易完成'
	}else if(value == 4){
		texts = '订单已取消'
	}
	return texts;
});
Vue.filter('businessType',function (value){
	var list = [
		"服饰","运动/户外","珠宝/配饰","美容护理",
		"家装家饰","书籍音像","手机","生活服务",
		"车品配件","家居用品","母婴","食品/保健",
		"3C数码","大家电","游戏/话费","玩乐/收藏",
		"医药健康","其他行业","鞋包"
	]
	var texts =list[value-1];
	return texts;
});

</script>
<style scoped>
	ul{display: none;}
	.isshow{display: block;}
	
</style>



