import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import information from '@/pages/information'
import informationDetail from '@/pages/informationDetail'
import addPost from '@/pages/addPost'

import shopInfo from '@/pages/index/transfer/shopInfo'   //网店信息
import transferred from '@/pages/index/transfer/transferred'   //网店
import transferredDetail from '@/pages/index/transfer/transferredDetail'  //网店发布
import transferredOrder from '@/pages/index/transfer/order'  //网店订单
import evaluation from '@/pages/index/transfer/evaluation'  //网店估价

import bidding from '@/pages/index/bidding/bidding'  //竞价
import biddingAdd from '@/pages/index/bidding/biddingAdd'  //竞价发布
import biddingDetail from '@/pages/index/bidding/biddingDetail'  //竞价详情
import canyujb from '@/pages/index/bidding/canyujb'  //参与竞标


import answer from '@/pages/index/answer/answer'  //回答
import answerAdd from '@/pages/index/answer/answerAdd'  //发布回答
import answerDetail from '@/pages/index/answer/answerDetail'  //回答详情

import classroom from '@/pages/index/classroom/classroom'  //课堂
import classroomType from '@/pages/index/classroom/classroomType'  //课堂类型列表
import classroomAdd from '@/pages/index/classroom/classroomAdd'  //发布课堂
import classroomDetail from '@/pages/index/classroom/classroomDetail'  //课堂详情

import recruit from '@/pages/index/recruit/recruit'  //招聘列表
import recruitAdd from '@/pages/index/recruit/recruitAdd'  //发布招聘
import recruitDetail from '@/pages/index/recruit/recruitDetail'  //招聘详情


import circle from '@/pages/circle/circle'  //圈子列表
import myCircle from '@/pages/circle/myCircle'  //我的圈子
import circleAdd from '@/pages/circle/circleAdd'  //发布圈子
import circleDetail from '@/pages/circle/circleDetail'  //圈子详情
import circleFind from '@/pages/circle/circleFind'  //发现圈子
import circleSet from '@/pages/circle/circleSet'  //圈子设置
import circleNews from '@/pages/circle/circleNews'  //圈子动态
import circleNewsAdd from '@/pages/circle/circleNewsAdd'  //发布圈子动态


import User from '@/pages/user/user' //个人中心
import collection from '@/pages/user/collection' //我的收藏
import follow from '@/pages/user/follow' //我的关注
import certification from '@/pages/user/certification' //实名认证
import release from '@/pages/user/release' //我的发布

import order from '@/pages/user/order' //我的订单－买家
import mjOrder from '@/pages/user/mjOrder' //我的订单－卖家
import offerOrder from '@/pages/user/offerOrder' //报价订单
import orderDetail from '@/pages/user/orderDetail' //订单详情


Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    {
      path: '/post/add',
      name: 'addPost',
      component: addPost
    },
    {
      path: '/information/:id',
      name: 'informationDetail',
      component: informationDetail
    },
    {
      path: '/user/user',
      name: 'User',
      component: User
    },
    {
      path: '/user/collection',
      name: 'collection',
      component: collection
    },
    {
      path: '/user/follow',
      name: 'follow',
      component: follow
    },
    {
      path: '/user/certification',
      name: 'certification',
      component: certification
    },
    {
      path: '/user/release',
      name: 'release',
      component: release
    },
    {
      path: '/user/order',
      name: 'order',
      component: order
    },
    {
      path: '/user/mjOrder',
      name: 'mjOrder',
      component: mjOrder
    },
    {
      path: '/user/offerOrder',
      name: 'offerOrder',
      component: offerOrder
    },
    {
      path: '/user/order/orderDetail/:id',
      name: 'orderDetail',
      component: orderDetail
    },
    
    {
      path: '/index/transfer/shopInfo/:id',
      name: 'shopInfo',
      component: shopInfo
    },
    {
      path: '/index/transfer/transferred',
      name: 'transferred',
      component: transferred
    },
    {
      path: '/index/transfer/transferredDetail',
      name: 'transferredDetail',
      component: transferredDetail
    },
    {
      path: '/index/transfer/order',
      name: 'transferredOrder',
      component: transferredOrder
    },
    {
      path: '/index/transfer/evaluation',
      name: 'evaluation',
      component: evaluation
    },
    {
      path: '/index/bidding/bidding',
      name: 'bidding',
      component: bidding
    },
    {
      path: '/index/bidding/biddingAdd',
      name: 'biddingAdd',
      component: biddingAdd
    },
    {
      path: '/index/bidding/biddingDetail/:id',
      name: 'biddingDetail',
      component: biddingDetail
    },
    {
      path: '/index/bidding/canyujb/:id',
      name: 'canyujb',
      component: canyujb
    },
    {
      path: '/index/answer/answer',
      name: 'answer',
      component: answer
    },
    {
      path: '/index/answer/answerAdd',
      name: 'answerAdd',
      component: answerAdd
    },
    {
      path: '/index/answer/answerDetail/:id',
      name: 'answerDetail',
      component: answerDetail
    },
    {
      path: '/index/classroom/classroom',
      name: 'classroom',
      component: classroom
    },
    {
      path: '/index/classroom/classroomAdd',
      name: 'classroomAdd',
      component: classroomAdd
    },
    {
      path: '/index/classroom/classroomType/:id',
      name: 'classroomType',
      component: classroomType
    },
    {
      path: '/index/classroom/classroomDetail/:id',
      name: 'classroomDetail',
      component: classroomDetail
    },
    {
      path: '/index/recruit/recruit',
      name: 'recruit',
      component: recruit
    },
    {
      path: '/index/recruit/recruitAdd',
      name: 'recruitAdd',
      component: recruitAdd
    },
    {
      path: '/index/recruit/recruitDetail/:id',
      name: 'recruitDetail',
      component: recruitDetail
    },
    {
      path: '/circle/circle',
      name: 'circle',
      component: circle
    },
    {
      path: '/circle/myCircle',
      name: 'myCircle',
      component: myCircle
    },    
    {
      path: '/circle/circleAdd',
      name: 'circleAdd',
      component: circleAdd
    },
    {
      path: '/circle/circleDetail/:id',
      name: 'circleDetail',
      component: circleDetail
    },
    {
      path: '/circle/circleFind',
      name: 'circleFind',
      component: circleFind
    },
    {
      path: '/circle/circleSet',
      name: 'circleSet',
      component: circleSet
    },
    {
      path: '/circle/circleNews',
      name: 'circleNews',
      component: circleNews
    },
    {
      path: '/circle/circleNewsAdd/:id',
      name: 'circleNewsAdd',
      component: circleNewsAdd
    }
  ]
})



