<template>
  <div class="contentHtml">
  	  <van-cell-group>
  	  	<van-field v-model="shop_name" label="店铺名称" placeholder="请输入" required/>
	  	<van-field v-model="shop_url" type="url" label="店铺网址" placeholder="请输入" required/>
	    <van-field v-model="coin_year" type="number" label="年营业额" placeholder="请输入" required/>
	    <van-field v-model="coin_tday" type="number" label="月营业额" placeholder="请输入" required/>
	    <van-field v-model="single_price" type="number" label="客单价" placeholder="请输入" required/>
	    <van-field v-model="qq_or_wx" label="QQ/微信号" placeholder="请输入"/>
	    <van-field v-model="phone_num" type="number" label="手机号码" placeholder="请输入" required/>
	    <van-field v-model="nasui_type" label="钠税人性质" placeholder="请输入" required/>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="submitBut">提交审核</div>
	 </div>
	
  </div>
</template>

<script>
$("title").text("店铺估价 ");
export default {
  name: 'contentHtml',
  data () {
    return {
    		fk_shop_id:'1',
    		shop_name:'',
    		shop_url:'',
    		coin_year:'',
    		coin_tday:'',
    		single_price:'',
    		qq_or_wx:'',
    		phone_num:'',
    		nasui_type:'',
    }
  },
  created:function(){
  	var _this = this;
  },
  methods: {
  	//店铺提交
	submitBut:function(){
		var _this = this;
//		var isSubmit = _this.judge();
//		if(!isSubmit){
//			return false;
//		}
  	 	$("#msgLoding").show();
    		var datas = {
	        "fk_shop_id":_this.fk_shop_id,         //店铺id
	        "shop_name":_this.shop_name,  //网店名称
	        "shop_url":_this.shop_url,  //店铺网址
	        "coin_year":_this.coin_year,      //年营业额
	        "coin_tday":_this.coin_tday,     //近30天营业额
	        "single_price":_this.single_price,   //客单价
	        "qq_or_wx":_this.qq_or_wx,   //qq或微信号
	        "phone_num":_this.phone_num,   //手机号码
	        "nasui_type":_this.nasui_type   //纳税人性质
	    }
    		
		$.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/shop_gujia.php',
	        data:datas,
	        success:function(result){
	            $("#msgLoding").hide();
    				_this.utilMsg("估价成功");
	        },
	        error : function(XmlHttpRequest, textStatus, errorThrown) {
	            alert(XmlHttpRequest1);
	        }
	   });
	},
	//ios跳转页面
	judge:function(){
		var _this = this;
		var getUser = localStorage.user;
		if(!getUser){
			_this.utilMsg("请先登录");
  	 		return false;
  	 	}
  	 	if(!_this.shop_name){
  	 		_this.utilMsg("请填写网店名称");
  	 		return false;
  	 	}
  	 	if(!_this.shop_url){
  	 		_this.utilMsg("请填写店铺网址");
  	 		return false;
  	 	}
  	 	if(!_this.coin_year){
  	 		_this.utilMsg("请填写年营业额");
  	 		return false;
  	 	}
  	 	if(!_this.coin_tday){
  	 		_this.utilMsg("请填写近30天营业额");
  	 		return false;
  	 	}
  	 	if(!_this.single_price){
  	 		_this.utilMsg("请填写客单价");
  	 		return false;
  	 	}
  	 	if(!_this.phone_num){
  	 		_this.utilMsg("请填写手机号码");
  	 		return false;
  	 	}
  	 	if(!_this.nasui_type){
  	 		_this.utilMsg("请填写纳税人性质");
  	 		return false;
  	 	}
  	 	return true;
	},
  	//ios跳转页面
	hrefTo:function(id){
		
	},
	onChange:function(picker, value, index) {
		alert(value+index)
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>
	
</style>



