<template>
  <div class="contentHtml">
  	  <van-cell-group>
	    <van-field v-model="title"  label="标题" placeholder="请输入" required/>
	    <div class="addList" style="padding-bottom:80px;">
			<h3>
	  			<span style="color:red;">*</span>
	  			<span class="inputLeft">问题描述:</span>
	  		</h3>
	  		<textarea v-model="excerpt" placeholder="请输入"></textarea>
	   </div>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="commitBut">提交审核</div>
	 </div>
  </div>
</template>

<script>
$("title").text("发布问题");
export default {
  name: 'contentHtml',
  data () {
    return {
    		title: "",
    		excerpt:''
    }
  },
  created:function(){
  	var _this = this;
  },
  methods: {
	commitBut(){
		var _this = this;
		
		var getUser = localStorage.user;
		if(getUser){
  	 		var fk_user_id =  JSON.parse(getUser).userid;
  	 	}else{
  	 		_this.utilMsg("请先登录");
  	 		return false;
  	 	}
  	 	if(!_this.title){
  	 		_this.utilMsg("请填写标题");
  	 		return false;
  	 	}
  	 	if(!_this.excerpt){
  	 		_this.utilMsg("请填写描述问题");
  	 		return false;
  	 	}
  	 	$("#msgLoding").show();
    		var datas = {
	        fk_user_id:fk_user_id,               //发布的用户id
	        ask_content:_this.excerpt,  //问题内容
	        title:_this.title      //问题标题。
		};
    		_this.utilAjax(_this.apiUrl+'/e/wxapi/public_ask.php',datas,function(res){
    			_this.excerpt = "";
    			_this.title = "";
    			$("#msgLoding").hide();
    			_this.utilMsg("发布成功");
    			
    			if(_this.utilIsLocalhost()==false){
				var content = {"type":"hrefTo","link":"/index/answer/answer","title":"回答列表"}
			    	var data = JSON.stringify(content);
			    ttf.nslog(data)
		  	}else{
		  		_this.$router.push('/index/answer/answer')
		  	}
    			
		},"post");
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>
	
</style>



