<script>
$("title").text("招聘");

export default {
  name: 'contentHtml',
  data () {
    return {
    	typeIndex: 0,
    	listDatas:[],
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	
	_this.getList();
	setTimeout(function(){
		_this.setwidth();
	},1)
	
  },
  methods: {
  	setwidth:function(){
  		var width = $(".hotClassroom li").width();
  		var lengthH = $(".hotClassroom li").length;
  		$(".hotClassroom").css("width",(width+10)*lengthH+"px");
  	},
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var per_page = 6;
	  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{"page":page,"per_page":per_page},function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
		    _this.this_page = _this.this_page+1;
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		})
  	},
	hrefTo(type,val){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			if(type=="hrefTo"){
				var content = {"type":"hrefTo","href":val}
			}else{
				var content = {"type":"href","id":val}
			}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		if(type=="hrefTo"){
	  			this.$router.push(val)
	  		}else{
	  			this.$router.push('/index/bidding/biddingDetail/'+val)
	  		}
  		}
	},
	typeIndexs(index){
		this.typeIndex = index;
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
