	.typebox{text-align: center;line-height:24px;padding: 20px 0; font-size: 16px;background: #fff;}
	.typebox span{width: 49%;display: inline-block;}
	.typebox span:first-child{border-right: 1px solid #CCCCCC;}
	.iconType{text-align: center;font-size:12px;background: #fff;margin-bottom:10px;overflow: hidden;}
	.iconType li{width: 25%;padding:15px 0;float: left;}
	.iconType li img{width:3rem;height: 3rem;}
	
	.initTitle{padding-bottom: 10px; border-bottom: 1px solid #eee;}
	.initTitle span:nth-child(1){border-left:4px solid #ff2222;padding-left:5px;}
	.initTitle span:nth-child(2){float: right;color: #999;font-size: 12px;}
	
	.hotClassroom{overflow: hidden;}
	.hotClassroom li{float: left;width:150px;padding:0 5px;}
	::-webkit-scrollbar{display:none;}
</style>



