<template>
  <div class="contentHtml">
  	  <ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo(listData.id)">{{listData.post_title}}</li>
    		</template>
     </ul>
     <div class="lodingDiv">正在加载。。。</div>
  </div>
</template>

<script>
$("title").text("我的收藏")
export default {
  name: 'contentHtml',
  data () {
    return {
		listDatas:[],
		this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	_this.getList();//默认请求
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
  },
  methods: {
  	//请求列表
    getList: function (event) {
    		$(".lodingDiv").text("正在加载。。。").show();
    		var _this = this;
    		var page = _this.this_page;
    		var per_page = 6;
    		var data = {"page":page,"per_page":per_page};
    		this.utilAjax('http://121.14.27.27:88/wp-json/favorites/v1/post',data,function(res){
    			$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
		    _this.this_page = _this.this_page+1;
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").show();
				var lengths = $(".listBox li").length;
			}
		},"get")
    },
	hrefTo(id){//ios跳转页面
		console.log(this.utilIsLocalhost())
		if(this.utilIsLocalhost()==false){
			var content = {"type":"href","id":id}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push('/information/'+id)
	  	}
	}
  }
}
</script>
<style scoped>
	.listBox li{line-height: 4rem;font-size: 1.5rem;}
</style>



