	        'cmp_addr':_this.wangdinArea.index,                 //公司地区
	        'trademark_joint':_this.trademarkJoint,   //商标是否连带，1连带，2不连带
	        'trademark_name':_this.trademarkName,     //商标名称
	        'trademark_owner':_this.trademarkOwner,   //商标持有者1企业持有，2个人持有
	        'trademark_type':_this.wangdinTrademark.index,      //商标种类
	        'trademark_time':"2017-02-03 20:20:20",     //商标申请时间
	        'trademark_own':_this.trademarkOwn,        //商标归属：1自有商标0授权
	        'bond2': _this.bond,                      //消费者保证金
	        'technical_money_fee':_this.technicalMoneyFee*1,  //技术年费
	        'price2':_this.price*1,                   //出售价格
	        'truename': _this.truename,            //真实姓名
	        'phone_num':_this.phoneNum,          //手机号
	        'qq_or_wx':_this.qqOrWx,             //qq或微信号
	   }
  		$("#msgLoding").show();
    		_this.utilAjax(_this.apiUrl+'/e/wxapi/addShop.php',datas,function(res){
    			$("#msgLoding").hide();
    			_this.utilMsg("发布成功")
		},"post");
  	},
  	//ios跳转页面
	hrefTo:function(id){
		
	},
	onChange:function(picker, value, index) {
		alert(value+index)
	},
    popShow: function (type) {
    		switch(type){
		    case "wangdian":
		        this.wangdian.type = true;
		        break;
		    case "wangdianNature":
		        this.wangdianNature.type = true;
		        break;
		    case "wangdianType":
		        this.wangdianType.type = true;
		        break;
		    case "wangdianTypeIs":
		        this.wangdianTypeIs.type = true;
		        break;
		    case "wangdianAddr":
		        this.wangdianAddr.type = true;
		        break;
		    case "wangdianlv":
		        this.wangdianlv.type = true;
		        break;
		    case "wangdianZl":
		        this.wangdianZl.type = true;
		        break;
		    case "wangdianPayType":
		        this.wangdianPayType.type = true;
		        break;
		    case "wangdinArea":
		        this.wangdinArea.type = true;
		        break;
		    case "wangdinTrademark":
		        this.wangdinTrademark.type = true;
		        break;
		    case "currentDate":
		        this.timeBox.type = true;
		        break;
		    default:
		        ""
		}
  		this.bankCard = false;
  	},
  	wangdianOnConfirm(value, index) {
      this.wangdian.val = value;
      this.wangdian.index = this.wangdian.indexList[index];
      this.wangdian.type = false;
    },
    wangdianNatureConfirm(value, index) {
      this.wangdianNature.val = value;
      this.wangdianNature.index = this.wangdianNature.indexList[index];
      this.wangdianNature.type = false;
    },
    wangdianTypeConfirm(value, index) {
      this.wangdianType.val = value;
      this.wangdianType.index = this.wangdianType.indexList[index];
      this.wangdianType.type = false;
    },
    wangdianTypeIsConfirm(value, index) {
      this.wangdianTypeIs.val = value;
      this.wangdianTypeIs.index = this.wangdianTypeIs.indexList[index];
      this.wangdianTypeIs.type = false;
    },
    wangdianAddrConfirm(value, index) {
      this.wangdianAddr.val = value;
      this.wangdianAddr.index = this.wangdianAddr.indexList[index];
      this.wangdianAddr.type = false;
    },
    wangdianlvConfirm(value, index) {
      this.wangdianlv.val = value;
      this.wangdianlv.index = this.wangdianlv.indexList[index];
      this.wangdianlv.type = false;
    },
    wangdianZlConfirm(value, index) {
      this.wangdianZl.val = value;
      this.wangdianZl.index = this.wangdianZl.indexList[index];
      this.wangdianZl.type = false;
    },
    wangdianPayTypeConfirm(value, index) {
      this.wangdianPayType.val = value;
      this.wangdianPayType.index = this.wangdianPayType.indexList[index];
      this.wangdianPayType.type = false;
    },
    wangdinAreaConfirm(value, index) {
      this.wangdinArea.val = value;
      this.wangdinArea.index = this.wangdinArea.indexList[index];
      this.wangdinArea.type = false;
    },
    wangdinTrademarkConfirm(value, index) {
      this.wangdinTrademark.val = value;
      this.wangdinTrademark.index = this.wangdinTrademark.indexList[index];
      this.wangdinTrademark.type = false;
    },
    onCancel(type) {
    		this.wangdian.type = false;
    		this.wangdianNature.type = false;
    		this.wangdianType.type = false;
    		this.wangdianTypeIs.type = false;
    		this.wangdianAddr.type = false;
    		this.wangdianlv.type = false;
    		this.wangdianZl.type = false;
    		this.wangdianPayType.type = false;
    		this.wangdinArea.type = false;
    		this.wangdinTrademark.type = false;
    },
    timeOnConfirm(value, index) {
      var myDate = value;
    	  var Y = myDate.getFullYear()//获取完整的年份(4位,1970-????)
    	  var M =  myDate.getMonth();//获取当前月份(0-11,0代表1月)
    	  var D = myDate.getDate()//获取当前日(1-31)
    	  var h = myDate.getHours(); //获取当前小时数(0-23)
    	  var m = myDate.getMinutes(); //获取当前分钟数(0-59)
    	  var s = myDate.getSeconds(); //获取当前秒数(0-59)
    	  var currentDate = Y+"-"+initTime(M)+"-"+initTime(D)+" "+initTime(h)+":"+initTime(m)+":"+initTime(s);
    	  function initTime(val){
    	  	var vals = val;
    	  	if(val < 10){
    	  		return "0"+vals;
    	  	}else{
    	  		return vals;
    	  	}
    	  }
    	  this.currentDate = currentDate;
      this.timeBox.type = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>
	
</style>



