<template>
  <div class="contentHtml">
  	  <ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo(listData.id)">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="listInfo">
	    					<!--<template v-for="tag in listData.tags">
	    						<span class="but1">{{tag}}</span>&emsp;
	    					</template>-->
	    					<span>{{listData.username}}&emsp;{{listData.newstime | timeFormatter}}&emsp;阅读:{{listData.onclick}}</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.titlepic"></div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("资讯");
var apihost = '';
export default {
  name: 'contentHtml',
  data () {
    return {
	    	listDatas:[],
	    	input:'',
	    	message:'',
	    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	apihost = _this.apiUrl;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		getList();
	  		return false;
	  	}
	  }
	})
	getList();
	function getList() {
    		$(".lodingDiv").text('').addClass("loding").show();
    		var per_page = 20;
    		var start = _this.listDatas.length;
    		var data = {
    		"fk_user_id":-1,
        'moduleid':1,    //模型ID (moduleid 与 classid 其中一项为必须)
        'classid':2,         //栏目ID (moduleid 与 classid 其中一项为必须)
        'start':start,          //开始显示条数 (分页时用，默认为0)
        "limit":per_page,           //显示总条数 (默认为15)
        "order":"newstime desc", //排序 (如 newstime desc)
        "keywor":''
		}
	  	$.post(_this.apiUrl+'/e/wxapi/list.php',data,function(res){
	  		
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},"json")
    }
  },
  methods: {
	hrefTo(id){//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":"/information/"+id,"title":"资讯详情"}
		    	var data = JSON.stringify(content);
		    	alertTtf(data)
//		    window.ttf.nslog(data)
	  	}else{
	  		this.$router.push('/information/'+id)
	  	}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
Vue.filter('imgFormatter',function (value){
	return apihost+value
});

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



