
export default{
  install(Vue,options){
  	Vue.prototype.getUser = function(name){  //utilAjax(name)
  		var getUser = localStorage.user;
  	 	if(getUser){
  	 		getUser =  JSON.parse(getUser);
  	 	}else{
  	 		return '';
  	 	}
  	 	if(name=='groupname'){
  	 		return getUser.groupname;
  	 	}else if(name=='guiboweb'){
  	 		return getUser.guiboweb;
  	 	}else if(name=='userfen'){
  	 		return getUser.userfen;
  	 	}else if(name=='userid'){
  	 		return getUser.userid;
  	 	}else if(name=='username'){
  	 		return getUser.username;
  	 	}else{
  	 		return '';
  	 	}
  	 };
  	//登录之后请求
  	 Vue.prototype.utilAjax = function(url,data,fn,type,user){  //utilAjax(url,data,fn,type)
  	 	var getUser = localStorage.user;
  	 	if(getUser){
  	 		getUser =  JSON.parse(getUser);
  	 	}else{
  	 		_this.utilMsg("请先登录")
  	 		return false;
  	 	}
	    	var ajaxUrl = url;
	    	var datas = data;
	    	var user = user?user:"yes";
	    	if(user=='yes'){
	    		datas.userid = getUser.userid;
	    		datas.username = getUser.username;
	    	}
	    	var ajaxType = type;
	    	var fn = fn;
    		var _this = this;
    		
  	 	$.ajax({
    			type:ajaxType,
    			data: datas,
    			//async: true,
			url: ajaxUrl,
			beforeSend: function(xhr) {
//  				xhr.setRequestHeader('Authorization', 'Bearer '+token);
            },
			success: function(data){
				console.log(data)
				return fn(data);
			},
            error: function(data){
            		$("#msgLoding").hide();
            		console.log(JSON.parse(data.responseText))
            		_this.utilMsg(JSON.parse(data.responseText).message)
            }
		});
  	 };
  	 //文件上传
  	 Vue.prototype.utilAjaxFile = function(url,data,fn,type){  //utilAjax(url,data,fn,type)
  	 	var getUser = localStorage.user;
  	 	var token = '';
  	 	if(getUser){
  	 		token =  JSON.parse(getUser).token;
  	 	}
	    	var ajaxUrl = url;
	    	var datas = data;
	    	var ajaxType = type;
	    	var fn = fn;
    		var _this = this;
  	 	$.ajax({
    			type:ajaxType,
    			data: datas,
    			async: true,
			url: ajaxUrl,
			processData: false,
			contentType: false,
			beforeSend: function(xhr) {
    				xhr.setRequestHeader('Authorization', 'Bearer '+token);
            },
			success: function(data){
				return fn(data);
			},
            error: function(data){
            		$("#msgLoding").hide();
            		console.log(JSON.parse(data.responseText))
            		_this.utilMsg(JSON.parse(data.responseText).message)
            }
		});
  	 };
  	 //判断环境
  	 Vue.prototype.utilIsLocalhost = function(){
  	 	var k_host = window.location.host;
  	 	return"localhost:8080"==k_host||"htmlpreview.github.io"==k_host
  	 };
  	 //api
	 Vue.prototype.apiUrl = "http://39.108.60.146";
 		//Vue.prototype.apiUrl = "http://192.168.1.103:8080";
  	 //api
  	 Vue.prototype.imgUrl = "http://39.108.60.146/e/wxapi/";
  	 
  	 //提示消息
  	 Vue.prototype.utilMsg = function(msgs){
  	  	var msgText = $("#msgText").text();
  	  	if(!msgText){
  	  		var html = '<div id="msgText">'+msgs+'</div>'
  	  		$("body").append(html);
  	  	}else{
  	  		$("#msgText").text(msgs);
  	  	}
  	  	$("#msgText").fadeIn();
  	  	setTimeout(function(){
  	  		$("#msgText").fadeOut();
  	  	},3000)
  	 };
  	 
  }
}


