<template>
  <div class="contentHtml" style="margin-bottom:40px;">
  	<div class="detailBox">
	  	<div class="title">{{datas.writer}}</div>
	  	<div class="idInfo" >
	  		<img class="fl imgBr" src="../assets/img.png" width="40" height="40"/>
	  		<span class="fl" style="margin-left: 10px;">
	  			<span>{{datas.writer}}</span><br/>
	  			<span class="color9" style="line-height: 2.5rem;">两小时前</span>
	  		</span>
	  		<span class="follow" v-bind:class="{ followOn: isFollow }" v-on:click="follow">关注</span>
	  	</div>
	  	<div class="content"></div>
	  	<div style="color: #999;font-size: 1.2rem;margin: 10px 0;">阅读：{{datas.onclick}}<span v-on:click="fabulous('post',datas.id)" class="fabulous" v-bind:class="{ fabulousOn: isFabulou }">{{datas.zhan_count}}</span></div>
  	</div>
  	<div class="initBox shareBox" style="margin-bottom: 10px;">
	  	<div class="font30 font30 subTite">分享</div>
	  	<div class="iconBox">
	  		<img v-on:click="share('weixin_circle')" src="../assets/pengyouquan@3x.png" width="44" height="44" />
	  		<img v-on:click="share('weixin_friend')" src="../assets/weixinhaoyou@3x.png" width="44" height="44" />
	  		<img v-on:click="share('weibo')" src="../assets/weibo@3x.png" width="44" height="44" />
	  	</div>
  	</div>
  	<!--<div class="initBox">
  		<div class="font30 subTite">相关推荐</div>
  	</div>
  	<ul class="listBox" style="margin-top: 0;">
  		<template v-for="listData in listDatas">
    		<li v-on:click="hrefTo(listData.id)">
    			<div class="leftBox">
    				<div class="listTitle">{{listData.title.rendered}}</div>
    				<div class="listInfo"><span class="but1">置顶</span><span>{{listData.author}} {{listData.date | timeFormatter}} 阅读：{{listData.id}}</span></div>
    			</div>
    			<div class="rightBox"><img v-bind:src="listData.post_pic"></div>
    		</li>
    		</template>
    	</ul>-->
    	<div class="initBox">
  		<div class="subTite font30">用户评价</div>
  		<ul class="commintBox">
  			<li v-for="comments in commentList">
  				<div class="authorInfo">
  					<img class="fl imgBr" v-bind:src="comments.userpic" width="30" height="30" style="margin-right: 10px;"/>
  					<span class="fl"> {{comments.uname}}<br/> {{comments.saytime}}</span>
  					<span v-on:click="fabulous('comment',comments.plid,comments.key)" class="fabulous" v-bind:class="{ fabulousOn: comments.isgood==1 }">{{comments.zcnum}}</span>
  				</div>
  				<div class="text">{{comments.saytext}}</div>
  			</li>
  		</ul>
    	</div>
    	<div class="lodingDiv loding"></div>
  	<div class="bottomNav">
  		<input v-model="value" type="text" placeholder="输入评论内容"/>
  		<img v-on:click="transmission" src="../assets/pinglun@3x.png" width="20" height="20"/>
  		<!--<img v-on:click="collection" src="../assets/shoucang@3x.png" width="20" height="20"/>-->
  		<span class="fabulous" v-bind:class="{ fabulousOn: isFabulou }" v-on:click="fabulous('post',datas.id)" style="height: 20px;width: 20px;"/></span>
  	</div>
  </div>
</template>
<script type="text/javascript">
 $("title").text("资讯详情");
export default {
  name: 'contentHtml',
  data () {
    return {
    		datas:'',
    		title:'',
    		isFollow: true,
    		isFabulou:false,
    		content:'',
    		listDatas:[],
    		value:'',
    		commentList:[],
    		commentPage:1
    }
  },
  created:function(){
  	var _self=this;
  	if(_self.getUser('userid')){
  		var userid = _self.getUser('userid');
  	}else{
  		var userid = '';
  	}
  	var datas = {
  		'classid':34,
		'news_id':_self.$route.params.id,
		'fk_user_id':userid,
  	}
  	//获取文章详情
  	$.post(_self.apiUrl+'/e/wxapi/get_article_by_id.php',datas,function(data){
  		console.log(data)
  		_self.datas = data[0];
		_self.title = data[0].writer;
		_self.isFollow = data[1].is_guanzhu;
		$(".content").html(data[0].newstext);
	},"json")
  	var user = localStorage.user;
  	if(user){
  		user = JSON.parse(user);
  	//判断是否已经点赞{
	    $.get(_self.apiUrl+'/e/wxapi/is_zhan.php',{"userid":user.userid,"fk_enews_id":_self.$route.params.id},function(res){
	  		_self.isFabulou = res;
		},"json")
  	}
  	 //判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_self.getList();
	  		return false;
	  	}
	  }
	})
	//评论列表
	_self.getList();
	
	
  },
  methods: {
  	getList: function (event) {
  		var _self=this;
  		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		var user_id = '';
  	 	}
		$(".lodingDiv").text('').addClass("loding").show();
		var start = _self.commentList.length;
    		var per_page = 10;
		var data = {
	        "classid":34,   //栏目id
	        "userid":user_id,       //用户id
	        "id":_self.$route.params.id,       //文章
	        "start":start,     //开始条数，默认0
	        "limit":per_page     ///每页条数，默认15
	    }
		$.post(_self.apiUrl+'/e/wxapi/commentslist.php',data,function(res){
			$(".lodingDiv").hide();
			for(var i = 0; i < res.length; i++) {
		      _self.commentList[start+i] = res[i];
		      _self.commentList[start+i].key = start+i;
		    }
			console.info(_self.commentList)
			 _self.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
  	},
  	//关注
    follow: function (event) {
    		var _this = this;
    		$("#msgLoding").show();
    		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg('请先登录');
  	 		return false;
  	 	}
    		var bgz_user_id = this.datas.userid;
    		var data = {"fk_user_id":user_id,"bgz_user_id":bgz_user_id};
    		$.get(_this.apiUrl+'/e/wxapi/guanzhu.php',data,function(res){
			$("#msgLoding").hide();
			if(_this.isFollow){
    				$(".follow").text("关注");
    				_this.utilMsg('已取消关注');
    				_this.isFollow = false;
    			}else{
    				$(".follow").text("已关注");
    				_this.utilMsg('关注成功')
    				_this.isFollow = true;
    			}
		},'json')
    		
    },
    //收藏
    collection: function (event) {
    		var _this = this;
    		$("#msgLoding").show();
    		var post_id = this.datas.id;
    		var status = '1';//0取消收藏 1收藏
    		var data = {"post_id":post_id,"status":status};
    		this.utilAjax('http://121.14.27.27:88/wp-json/favorites/v1/post',data,function(res){
    			$("#msgLoding").hide();
    			_this.utilMsg('收藏成功')
			console.log(res)
		},"post")
    },
    //点赞 
    fabulous: function (type,id,key) {
    		var keys = key;
    		console.log(keys)
    		$("#msgLoding").show();
    		var _this = this;
    		var user = localStorage.user;
  		if(!user){
  			_this.utilMsg('请先登录')
  			return false;
  		}
  		user = JSON.parse(localStorage.user);
    		if(type=="post"){
    			var post_id = id;
	    		var datas = {"fk_enews_id":post_id,"userid":user.userid}
    			$.get(_this.apiUrl+'/e/wxapi/news_zhan.php',datas,function(res){
				$("#msgLoding").hide();
	    			if(_this.isFabulou){
	    				_this.utilMsg('不喜欢');
	    				_this.datas.zhan_count = _this.datas.zhan_count*1-1
	    				_this.isFabulou = false;
	    			}else{
	    				_this.utilMsg('点赞成功')
	    				_this.datas.zhan_count = _this.datas.zhan_count*1+1
	    				_this.isFabulou = true;
	    			}
			},'json')
    			
    		}else{
    			var getUser = localStorage.user;
			if(getUser){
	  	 		var user_id =  JSON.parse(getUser).userid;
	  	 	}else{
	  	 		_this.utilMsg("请先登录");
	  	 		return false;
	  	 	}
	    		var comment_id = id;
	    		var datas = {
	    			'classid':2,
	    			'userid':user_id,
				'id':_this.$route.params.id,
				'plid':comment_id
	    		}
    			var ajaxUrl = _this.apiUrl+'/e/wxapi/commentszc.php';
    			this.utilAjax(ajaxUrl,datas,function(res){
	    			$("#msgLoding").hide();
	    			if(_this.commentList[keys].isgood==1){
	    				_this.utilMsg('取消点赞');
	    				_this.commentList[keys].zcnum = _this.commentList[keys].zcnum*1-1
	    				_this.commentList[keys].isgood = 0;
	    			}else{
	    				_this.utilMsg('点赞成功')
	    				_this.commentList[keys].zcnum = _this.commentList[keys].zcnum*1+1
	    				_this.commentList[keys].isgood = 1;
	    			}
	    			_this.$forceUpdate();
			 },"post")
    		}
    },
    //分享
    share: function (type) {
    		var title = this.datas.title.rendered;
    		var info = this.datas.slug;
    		var img = this.datas.slug;
    		var id = this.datas.id;
    		var content = {"type":type,"title":title,"info":info,"img":img,"id":id}
        	var data = JSON.stringify(content);
        	console.log(data)
        	ttf.nslog(data)
    },
	hrefTo:function(id){//ios跳转页面
	  	var content = {"type":"href","id":id}
	    	var data = JSON.stringify(content);
	    	if(!this.utilIsLocalhost){
			ttf.nslog(data)
	  	}else{
	  		this.$router.push('/information/'+id)
	  	}
	},
	//评论
    transmission: function () {
    	    var _this = this;
    		var user = localStorage.user;
    		if(!user){
    			_this.utilMsg("请先登录")
    			return false;
    		}
    		user = JSON.parse(user);
    		var content = this.value;
    		if(content==''){
    			_this.utilMsg("内容为空")
    			return false;
    		}
    		$("#msgLoding").show();
    		var post_id = this.$route.params.id
    		var token =  user.token;
		var datas = {
		        "classid":34, //(必须)栏目ID
		        "id":post_id,      //(必须)文章id号
		        "saytext":content, //(必须)评论内容
		        "username":user.username,//用户名(必须)
		        "userid":user.userid  //用户ID(必须)
		   };
    		$.post(_this.apiUrl+'/e/wxapi/comments_subm.php',datas,function(res){
			console.log(res)
			$("#msgLoding").hide();
			_this.value = '';
			_this.utilMsg('评论成功')
			_this.commentList = [];
			_this.getList();
		},"json")
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.fabulous{float: right;background: url(../assets/dadianzan@3x.png)left no-repeat;background-size: 1.5rem 1.5rem;padding-left:2rem;font-size: 1.2rem;}
	.fabulousOn{background: url(../assets/dadianzanbianse@3x.png)left no-repeat;background-size: 1.5rem 1.5rem;}
	.subTite{line-height:50px;border-bottom: 1px solid #f7f7f7;text-align: center;}
	.detailBox{background: #fff;margin-bottom: 10px;padding: 10px;}
	.detailBox .title{font-size: 1.5rem;color: #333;}
	.detailBox .idInfo{overflow: hidden;margin: 30px 0;}
	.detailBox .idInfo .follow{float: right;padding: 6px 10px;color: #ff1919;border: 1px solid #ff1919;border-radius: 5px;-webkit-border-radius: 5px;}
	.detailBox .idInfo .followOn{color: #ccc;border: 1px solid #ccc;}
	
	.detailBox .content{font-size: 1.2rem;color: #666;}
	.shareBox .title{line-height:50px;border-bottom: 1px solid #f7f7f7;}
	.shareBox .iconBox{display:flex;display:-webkit-flex;justify-content:space-around;padding: 10px 0;}
	
	.commintBox li{padding: 10px 0;border-bottom: 1px solid #f7f7f7;}
	.commintBox li .authorInfo{color: #999;overflow: hidden;margin-bottom: 10px;}
	.commintBox li .text{margin-left:40px ;font-size: 1.5rem;}
	.bottomNav{
		height:30px;position: fixed;bottom: 0;left: ;width: 100%;
		background: #f7f7f7;display:flex;display:-webkit-flex;
		justify-content:space-around;padding:10px 0;
	}
	.bottomNav input{
		width:17rem;line-height: 2rem;height: 2rem;
		border-radius:100px;-webkit-border-radius:100px;
	}
</style>



