
<template>
  <div class="contentHtml">
  	  <van-cell-group>
  	  	<van-cell title="订单类型"  value="竞标" />
	  	<van-cell title="订单编号"  v-bind:value="jbDetail.order_num" />
	  	<van-cell title="报价时间"  v-bind:value="jbDetail.time"/>
	  	<van-cell>
		    <template slot="title">
		      <span class="van-cell-text">订单总额</span>
		    </template>
		    <span>¥{{jbDetail.price}}</span>
  		</van-cell>
  		<van-cell title="竞标标题"  v-bind:value="jbDetail.title" />
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	  <van-cell-group>
	  	<van-field  label="我的报价" type="number" v-model="price" placeholder="请输入" required/>
	  </van-cell-group>
	  <div class="addList" style="padding-bottom:80px;">
		<h3 class="imgH3">
  			<span style="color:red;">*</span>
  			<span class="inputLeft">描述方案:</span>
  		</h3>
  		<textarea v-model="excerpt" placeholder="请输入"></textarea>
    </div>
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="commintBut">确认</div>
	 </div>
  </div>
</template>

<script>
setTimeout(function(){
	$("title").text("订单信息");
},1)
import { Dialog } from 'vant';
export default {
  name: 'contentHtml',
  data () {
    return {
    		jbDetail:'',
    		excerpt:'',
    		radio:'1',
    		price:''
    }
  },
  created:function(){
  	var _this = this;
  	//获取报价信息
  	$.post(_this.apiUrl+'/e/wxapi/tbj_page.php',{"order_id":_this.$route.params.id},function(res){
  		console.log(res)
  		_this.jbDetail = res[0];
	},"json")
  },
  methods: {
  	//确认
	commintBut:function(id){
		var _this = this;
		if(!_this.price){
			_this.utilMsg("请输入金额");
			return false;
		}else if(!_this.excerpt){
			_this.utilMsg("请输入描述");
			return false;
		}
		var datas = {
			'order_id':_this.$route.params.id,
			'price':_this.price,
			'baojia_miaoshu':_this.excerpt,
	    };
		Dialog.confirm({
		  title: '',
		  message: '确认报价后无法重新修改<br/>确认报价？',
		  confirmButtonText:'确认报价',
		  cancelButtonText:'考虑考虑'
		}).then(() => {
		    // on confirm
		  	$.post(_this.apiUrl+'/e/wxapi/tbj.php',datas,function(res){
		  		console.log(res)
		  		//ios跳转页面
//				if(_this.utilIsLocalhost()==false){
//					var content = {"type":"hrefTo","link":'val',"title":'title',}
//				    	var data = JSON.stringify(content);
//				    ttf.nslog(data)
//			  	}else{
//			  		_this.$router.push(val)
//		  		}
			},"json")
		  
		}).catch(() => {
		  // on cancel
		});
	},
	//ios跳转页面
	hrefTo:function(id){
		
	},
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
	.van-dialog__content .van-dialog__message{text-align: center;line-height: 50px;}
	.addList{margin-bottom: 10px;font-size: 14px;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 14px; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px 10px 6px;margin: 0;font-size: 14px;}
	.addList input{border: 0;font-size: 14px;width: 75%;}
	.addList .inputLeft{width: 25%;}
</style>



