<template>
  <div id="app">
    <app-header></app-header>
    <app-nav></app-nav>
    <router-view/>
    <!--<app-footer></app-footer>-->
    <div id="msgLoding"></div>
  </div>
</template>
<script>
//设置字体
setFont();
$(window).resize(function(){
	setFont();
})
function setFont(){
	var winWidth = $('body').width();
	if(winWidth<769){
		$("html,body").css("font-size",20*(winWidth/640)+"px");
	}
}
export default {
  name: 'app'
}

</script>

<style>

body,html{padding: 0;margin: 0;border: 0;max-width: 768px;margin: 0 auto;background: #f7f7f7;}
ul,li,a,dl,dd,dt,img,a,p,span,div{
	padding: 0;margin: 0;border: 0;
}
h1, h2 ,h3,h4,h5,h6{font-weight: normal;}
ul{list-style-type: none;}
a {color: #333;text-decoration: none;-webkit-tap-highlight-color:transparent;}
::-webkit-input-placeholder{color:#999;}
input{
	-webkit-appearance:none;
	appearance:none;outline:none;
	-webkit-tap-highlight-color:rgba(0,0,0,0);
	border-radius:0;
	border: 1px solid #eee;
	padding: 4px 8px;
}
.font30{font-size:1.5rem;}
.text-c{text-align:center;}
.text-l{text-align:left;}
.text-r{text-align:right;}
.text-oh1{overflow: hidden;text-overflow: ellipsis;white-space: nowrap;}
.text-oh2{
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp:2;
	overflow: hidden;
}
.initBox{background: #fff;padding: 10px;}
.initTitle{font-size: 16px;color: #333;}
.textImg{vertical-align: middle;}
.fl{float: left;}
.fr{float: right;}
.oh{overflow: hidden;}
.color9{color: #999;}
.color6{color: #666;}
.color3{color: #333;}
.colorA{color: #ff1818;}
.font12{font-size: 12px;}
.font14{font-size: 14px;}
.font16{font-size: 16px;}
.font18{font-size: 18px;}
.imgBr{border-radius: 50%;-webkit-border-radius: 50%;}
.flex{display: flex;display: -webkit-flex;justify-content:space-between;}
.flexLeft{display: flex;display: -webkit-flex;justify-content:flex-start;}
.width15{width: 15%;}
.width25{width: 25%;}
.width33{width: 33.3%;}
.width50{width: 50%;}
.width66{width: 66.66%;}
.width75{width: 75%;}
.width85{width: 85%;}
.width100{width:100%;}
.mb10{margin-bottom: 10px;}
.lineHeight{line-height:20px; }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  font-size: 1rem;
  margin-bottom:50px;
  width: 100%;
  overflow: hidden;
}

.line{border-bottom: 1px solid #ebebeb;}
#msgText{
	background: rgba(0,0,0,0.7);padding:8px 10px;border-radius:3px;-webkit-border-radius:3px;
	max-width:90%;position: fixed;left:50%;top: 50%;color: #fff;font-size: 1rem;display: none;
	-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);z-index: 9999;
}
#msgLoding{
	border-radius:3px;-webkit-border-radius:3px;width:8rem;height:8rem;
	background:url(assets/Loding.gif) center no-repeat #353535;background-size:5rem 5rem;
	position: fixed;left:50%;top: 50%;-webkit-transform: translate(-50%, -50%);display: none;
	transform: translate(-50%, -50%);z-index: 9999;opacity: 0.8;-webkit-opacity: 0.8;
}
#htmlLoding{
	border-radius:3px;-webkit-border-radius:3px;width:7rem;height:7rem;
	background:url(assets/Loding.gif) center no-repeat #353535;background-size:4rem 4rem;
	position: fixed;left:50%;top: 50%;-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);z-index: 9999;opacity: 0.6;-webkit-opacity: 0.6;
}


.listBox{}
.listBox li{padding:10px;border-bottom: 1px solid #eee; background: #fff;overflow: hidden;display: flex;justify-content:space-between}
.listBox li a{display: flex;display: -webkit-flex; /* Safari */justify-content:space-between;}
.listBox li .leftBox{float: left;}
.listBox li .leftBox .listInfo{color: #999;font-size: 1rem;margin-top: 0.5rem;}
.listBox li .leftBox .listInfo .but1{border:1px solid #eee;font-size:1rem; margin-right: 5px;color: #ed7655; padding: 0px 6px;border-radius:5px;-webkit-border-radius:5px;}
.listBox li .leftBox .listInfo .but2{border:1px solid #ed7655;background: #ed7655;color: #fff;}
.listBox li .rightBox{float: right;}
.listBox li .rightBox img{width: 10rem;height: 7rem;}
.listBox li .listTitle{font-size: 16px;color: #333;height:60px;}

.but-80-red{width: 80%;margin: 0 auto; background: #ff1919;color: #fff;font-size: 1.3rem;text-align: center;line-height:3.5rem;border-radius:5px;-webkit-border-radius:5px;}
.lodingDiv{text-align: center;background: #F7F7F7;height: 60px; line-height: 60px;font-size: 1.2rem;}
.loding{background:url(assets/backLoding.gif) center center no-repeat;background-size:2.3rem 2.3rem;opacity: 0.6;-webkit-opacity: 0.6;}
</style>



